<template>
  <div class="All">
     <my-head class='head1' id="head1"> 
      <strong slot='left' class="head_left">
         <router-link :to="{path: '/select'} ">
            <img src="../../assets/Select/剑.png" alt="" class="sech">
         </router-link>
      </strong>
      <strong slot='right' class="head_center">
          <span>可做菜品</span>
      </strong>
    </my-head >
      <ul v-for="(item,index) in foodsArr">
          <li class="left"  @click="Fn(index)">
             <router-link :to="{path: '/MenuDetail'}">
                <img v-bind:src="item.url1" class="left_img">
             </router-link>   
          </li>
          <li class="center">

            <p>{{item.title}}</p>
            <img v-bind:src="item.url2">
            <img v-bind:src="item.url2">
            <img v-bind:src="item.url2">
            <img v-bind:src="item.url3">
            <p></p>
            <span>家常菜</span>
            <span>白领</span>
            <span>香辣</span>
            <span>{{item.time}}</span>
          </li>
           <li class="right" @click=press(index)>
               <img v-bind:src="item.url4" class="rightlink_img" v-show="show[index]" >
               <img v-bind:src="item.url4_4" class="rightlink_imgtow" v-show="!show[index]">   
           </li> 
           <li v-show="hide"></li>
      </ul>
  </div>
</template>

<script>
import {mapGetters,mapActions} from 'vuex'

import Head from '../Public/Header.vue'
import Nav from '../Public/Nav.vue';
import Hnav from './Hnav.vue';
import Vue from 'vue';
import { Lazyload } from 'mint-ui';
import { Toast } from 'mint-ui'
import localEvent from '../../vuex/local';
Vue.use(Lazyload);
export default {
   data(){
    return{
       show:[true,true,true,true,true],
       hide:true
    }
   },
   methods:{
     ...mapActions([
      "setMenuFoodsArr"]),
     Fn:function(index){
      localEvent.StorageSetter("Arr",this.foodsArr[index]);
      localEvent.StorageSetter("index",index); 
      // console.log(this.foodsArr[index]);
      // console.log(index)
     },
     press:function(index){
         this.show[index]=!this.show[index];
          this.hide=!this.hide;
          if (this.show[index]==false) {
        Toast({
          message: '收藏成功',
          iconClass: 'icon icon-success',
          duration:1300
        });
        return;
      }else {
        this.show[index]=false;
           Toast({
          message: '已经收藏',
          iconClass: 'icon icon-success',
          duration:1300
            });  
        }
      }
   },
  computed: mapGetters({
     foodsArr:'getMenuFoodsArr'
  }),
   components:{
    "my-head":Head,
    'my-nav':Nav
   }
}
</script>

<style lang="stylus" scoped>
@import '../../assets/common/stylus/common.styl'
.All
    width pxtorem(750px)
    background #ebeae4
    overflow hidden
   
ul
     width pxtorem(750px) 
     overflow hidden

ul:nth-of-type(1)
   margin-top pxtorem(130px)     
     
.left
    width pxtorem(278px)
    height pxtorem(241px) 
    margin-left  pxtorem(20px)
    margin-top pxtorem(26px)
    float left
.left_img
     width pxtorem(278px)
     height pxtorem(241px)
.center 
     width pxtorem(240px)
     height pxtorem(240px)
     float left  
     margin-left pxtorem(20px)  
     margin-top  pxtorem(46px)
.center>p:nth-of-type(1)
     font-size pxtorem(28px) 
     color #6e6e6e          
.center>img 
    width pxtorem(35px)
    height pxtorem(29px)
    margin-top pxtorem(25px)
.center>img :nth-of-type(2),.right>img :nth-of-type(2)
    margin-left pxtorem(18px)
    margin-right pxtorem(18px)
.center>p:nth-of-type(2)
    clear both
    height  pxtorem(20px)   
.center>span
   margin-top  pxtorem(28px)
.center>span:nth-of-type(4)
   display block 
.right 
   width pxtorem(45px)
   height pxtorem(45px)
   float left
   margin-left pxtorem(80)
   margin-top pxtorem(206px)
   position relative
.rightlink_img,.rightlink_imgtow
   width pxtorem(45px)
   height pxtorem(45px) 
.rightlink_imgtow
  position absolute
  top pxtorem(0px)
  left pxtorem(0px)    
.head_left>.sech,.head_center span 
    position fixed
    top pxtorem(40px)
.sech
    width pxtorem(25px)
    height pxtorem(39px)
    left pxtorem(30px) 
   
.head_center span
    width pxtorem(140px)
    height pxtorem(50px)
    left pxtorem(308px)
    line-height  pxtorem(50px)
    font-size pxtorem(30px)
#head1
  background #48382b 

   
                    
</style>



