<template>
  <div class="wrap">
    <div class="inner clear">
      <span>绿色世界</span>
      <span @click="seeMoreFn">下拉查看更多</span>
    </div>
    <div class="content">
      <ul v-for='(item,index) in list' @click="detailFn(index)">
        <li>
          <img v-bind:src="item.src" alt="">
        </li>
        <li>
          <div>
            <p>{{item.title}}</p>
            <p>{{item.type}}</p>
            <p>￥{{item.originPrice}}</p>
          </div>
          <div class="hot"><span>热销中</span><span>{{item.buy}}</span></div>
        </li>
      </ul>
      <infinite-loading @infinite="infiniteHandler">
        <span slot="no-more" class="noMore">我们是有底线的</span>
      </infinite-loading>
    </div>
  </div>
</template>

<script>
// import axios from 'axios';
import {mapGetters,mapActions} from "vuex";
import InfiniteLoading from 'vue-infinite-loading';
export default {
  data () {
    return {
      list: []
    }
  },
  //2.注册组件
  components:{
    InfiniteLoading
  },

  created:function(){
    
  },
  //实时监听页面数据的变化
  computed:mapGetters({
    foodieArr:'getFoodie'
  }),
  methods:{
    ...mapActions([
    "getFoodie","setShopCarGoods"]),
    detailFn:function(index){
      var arr=[];
      arr.push(this.$store.state.foodie[index])
      this.setShopCarGoods(arr);
      this.$router.push("/goodsdetail")
    },
    seeMoreFn:function(){
      
    },
    infiniteHandler($state) {
      if(this.foodieArr.length==this.list.length){
        $state.complete();
      }else{
        setTimeout(() => {
          const temp = [];
          for (let i = this.list.length ; i <=  this.list.length + 5&&i <this.foodieArr.length; i++) {
            temp.push(this.foodieArr[i]);
          }
          this.list = this.list.concat(temp);
          $state.loaded();
        }, 1000);
      }
    }
  },
  mounted:function(){

  }
  
}
</script>


<style lang='stylus' scoped>
  @import '../../assets/common/stylus/common.styl'
  .wrap
    width pxtorem(750px)
    margin-top pxtorem(20px)
    background-color #FFF
    padding-top pxtorem(30px)
    padding-bottom pxtorem(100px)
    .inner
      width pxtorem(710px)
      margin auto
      font-size pxtorem(32px)
      font-weight bolder
      letter-spacing pxtorem(2px)
      color #92c808
      margin-bottom pxtorem(20px)
      span:nth-child(2)
        float right
        color #a0a0a0
    .content
      background-color: #ebeae4
      ul:last-child
        margin-bottom 0
      ul
        width pxtorem(710px)
        padding 0 pxtorem(20px)
        margin auto
        margin-bottom pxtorem(30px)
        background-color: #fff
        li:nth-child(1)
          display inline-block
          margin-right pxtorem(20px)
          img
            width pxtorem(250px)
            height pxtorem(220px)
        li:nth-child(2)
          display inline-block
          vertical-align top
          color #4d4d4d
          font-size pxtorem(32px)
          line-height pxtorem(50px) 
          p:nth-child(2)
            font-size pxtorem(26px)
            color #747474
          p:nth-child(3)
            font-size pxtorem(32px) 
            color #8dc33b
            line-height pxtorem(60px)
          .hot
            span:nth-child(1)
              display inline-block
              width pxtorem(100px)
              height pxtorem(40px)
              border pxtorem(2px) solid red
              border-radius pxtorem(10px)
              text-align center
              line-height pxtorem(40px)
              font-size pxtorem(24px)
              background-color red
              color #fff
              letter-spacing pxtorem(2px)
            span:nth-child(2)
              color purple
              margin-left pxtorem(30px)
        .noMore
          font-size pxtorem(40px)
</style>


