import Vue from 'vue'
import Router from 'vue-router'
import home from  '@/components/Home/Homepage'
import GoodsDetailPage from  '@/components/Home/GoodsDetailPage'

import SelectPage from '../components/Select/SelectPage.vue'
import FindPage from '../components/Find/FindPage.vue'
import MyPage from '../components/My/MyPage.vue'
import Nav from  '@/components/Public/nav.vue'
import Location from  '@/components/Home/Location.vue'
import Enter from  '@/components/My/Enter.vue'
import EntryAfter from  '@/components/My/EntryAfter.vue'
import MyOrderPage from '../components/My/MyOrderPage.vue'
import ShopCarPage from '../components/My/ShopCarPage.vue'
import SetPage from '../components/My/SetPage.vue'
import HelpCenterPage from '../components/My/HelpCenterPage.vue'
import ConfirmPage from '../components/My/ConfirmPage.vue'
import MyVipPage from '../components/My/MyVipPage.vue'
import ServicePage from '../components/My/ServicePage.vue'

import Circlecomment from '@/components/Find/Circlecomment.vue'
import Menu from '@/components/Select/Menu.vue'
import MenuDetail from '@/components/Select/MenuDetail.vue'
import evaluate from '@/components/Find/evaluate.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/select',
      component: SelectPage
    },
    {
      path: '/find',
      component: FindPage
    },
    {
      path: '/my',
      component: MyPage
    },
    {
      path: '/location',
      component: Location
    },
    {
      path: '/goodsdetail',
      component: GoodsDetailPage
    },
    {
      path: '/enter',
      component: Enter
    },
    {
      path: '/entryafter',
      component: EntryAfter
    },
    {
      path: '/myorder',
      component: MyOrderPage
    },
    {
      path: '/shopcar',
      component: ShopCarPage
    },
    {
      path: '/set',
      component: SetPage
    },
    {
      path: '/help',
      component: HelpCenterPage
    },
    {
      path: '/confirm',//结算页面
      component: ConfirmPage
    },
    {
      path: '/Circlecomment',
      component: Circlecomment
    },
    {
      path: '/myvip',
      component: MyVipPage
    },
    {
      path:'/service',
      component:ServicePage
    },
    {
      path: '/Menu',
      component: Menu
    },
    {
      path: '/MenuDetail',
      component: MenuDetail
    },
    {
      path: '/evaluate',
      component: evaluate
    }
  ]
})



