<template>
  <div class="wrap">
    <my-head id='head'> 
      <strong slot='left' @click='locationFn'>{{city}}
        <img src="../../assets/Home/down.png" alt="" class="down">
      </strong>
      <strong slot='center' class="center">
        <img src="../../assets/Home/sech.png" alt="" class="sech">
        <input type="text" class="input" placeholder="搜索食材、菜品" @click="sechFn">
      </strong>
      <strong slot='right' class="shopcar">
        <img src="../../assets/Home/shopcar.png" alt="" @click='shopcarFn'><span v-show='goodCountBol'>{{shop.length}}</span>
      </strong>
    </my-head>
    <my-swiper v-bind:scrol="contentArr"></my-swiper>
    <pnic-buy></pnic-buy>
    <my-choice></my-choice>
    <my-foodie></my-foodie>
    <my-nav></my-nav> 
  </div>
</template>

<script>
import Head from '../Public/Header.vue'
import Swiper from '../Public/Swiper.vue'
import PnicBuy from './PnicBuy.vue'
import Choice from './Choice.vue'
import Foodie from './Foodie.vue'
import Nav from '../Public/Nav.vue'
import {mapGetters,mapActions} from "vuex"
import axios from 'axios';
export default {
  data () {
    return {
      contentArr:[
        {url:require("../../assets/Home/pic1.png")},
        {url:require("../../assets/Home/pic2.png")},
        {url:require("../../assets/Home/pic3.png")}
      ],
      goodCountBol:false
      
    }
  },
  //2.注册组件
  components:{
    "my-head":Head,
    "my-swiper":Swiper,
    "pnic-buy":PnicBuy,
    "my-choice":Choice,
    "my-foodie":Foodie,
    "my-nav":Nav
  },

  created:function(){
    
  },
  //实时监听页面数据的变化
  computed:mapGetters({
      city:'getCityName',
      goods:"getGoodsMessage",
      shop:"getShopCarPage"
  }),
  methods:{
    ...mapActions([
      "setCityName",
      "setGoodsMessage",
      
      ]),
    locationFn:function(){
      this.$router.push('/location')
    },
    sechFn:function(){
      this.$router.push("/select")
    },
    shopcarFn:function(){
      this.$router.push("/shopcar")
    }
  },
  mounted:function(){
    this.setGoodsMessage();
    if (this.shop.length>0) {
      this.goodCountBol=true;
    }
  }
  
}
</script>


<style lang='stylus' scoped>
  @import '../../assets/common/stylus/common.styl'
  .wrap
      font-size pxtorem(32px)
      background-color: #ebeae4
      width pxtorem(750px)
      overflow hidden
      #head
        background-color #106602
        strong
          .down
            width pxtorem(23px)
          .input
            background-color #65d600
            color #fff
            width pxtorem(390px)
            height pxtorem(50px)
            border-radius pxtorem(20px)
            margin-left pxtorem(-55px)
            outline none
            padding-left pxtorem(60px)
            font-size pxtorem(26px)
          .news
            vertical-align middle 
            width pxtorem(40px)
        .center
          position relative
          .sech
            position absolute
            top pxtorem(13px)
            left pxtorem(-35px)
            width pxtorem(30px)
            z-index 10 
  .shopcar
    width pxtorem(100px)
    height pxtorem(100px)
    position fixed
    top pxtorem(15px)
    right pxtorem(20px)
    z-index 100 
    border-radius 50% 
    img
      position absolute
      top 0
      left 0
      right 0
      bottom 0
      margin auto
      width pxtorem(50px)
    span 
      position absolute
      top pxtorem(10px)
      right pxtorem(5px)
      width pxtorem(32px)
      height pxtorem(32px)
      border-radius 50%
      background-color red 
      text-align center
      line-height pxtorem(32px)
      font-size pxtorem(26px)
      color #fff
      font-weight bold
 
</style>


