<template>
  <div class="wrap1">
    <my-head class='head'> 
      <strong slot='left'>
        <img src="../../assets/My/right.png" alt="" class="back" @click="backFn">
      </strong>
      <strong slot='center'>
        购物车
      </strong>
      <strong slot='right' class ="right" @click='editFn'>
        编辑
      </strong>
    </my-head>
    <div class="inner">
      <div class="content" v-for='(item,index) in contentArr'>
        <div @click='selectFn(index)'>
          <img src="../../assets/My/tick.png" alt="" v-show='tickBol[index]'>
          <img src="../../assets/My/tick1.png" alt="" v-show='!tickBol[index]'>
          <img :src="item.src" alt="">
        </div>
        <ul>
          <li>{{item.goodsName}}</li>
          <li>{{item.effect}}</li>
          <li>
            <span>￥{{item.goodsPrice.toFixed(2)}}</span>
            <div><span @click='jianFn(index)'>-</span><span class="mess" ref="many">{{item.bum}}</span><span @click='addFn(index)'>+</span></div>
          </li>
          <li class="del" @click='delFn(index)' v-show='delBol'>删除</li>
        </ul>
        <p v-show='bool'></p>
      </div>
    </div>

    <div class="bottom">
      <ul>
        <li @click='AllFn'>
          <img src="../../assets/My/tick2.png" alt="" class="All" v-show='AllBol'>
          <img src="../../assets/My/tick1.png" alt="" class="All" v-show='!AllBol'>
          全选</li>
        <li>合计: <span>￥{{sumPrice.toFixed(2)}}</span><p>(不含运费)</p></li>
        <li @click='SettlementFn'>结算(<span>{{checkNum}}</span>)</li>
      </ul>
    </div>
  </div>
</template>

<script>
import Head from '../Public/Header.vue'
// 引入axios模块
// import axios from 'axios';
import {mapGetters,mapActions} from "vuex";
export default {
  data () {
    return {
      tickBol:[],
      AllBol:true,
      sumPrice:0,
      checkNum:0,
      delBol:false,
      bool:false,
      mes:1
    }
  },
  //2.注册组件
  components:{
    "my-head":Head
  },

  created:function(){
    
  },
  //实时监听页面数据的变化
  computed:{
    ...mapGetters({
      contentArr:'getShopCarPage',
      bottomArr:"getSettlement",
      getbuyNum:"getbuyNum"
    })
  },
  methods:{
    ...mapActions([
      "setShopArr",
      "setbuyNum"
    ]),
    backFn:function(){
      this.$router.go(-1)
    },
    // 编辑
    editFn:function(){
      this.delBol=!this.delBol;
    },
    //删除订单
    delFn:function(index){
      this.contentArr.splice(index,1);
      this.jisuanFn();
    },
    //计算总额
    jisuanFn:function(){
      this.sumPrice =0;
      this.checkNum =0;
      for(var i=0;i<this.contentArr.length;i++){
        if (this.tickBol[i]==false) {
          this.sumPrice+=this.contentArr[i].goodsPrice;
          this.checkNum++;
        }
      }
    },
    //选中商品
    selectFn:function(index){
      this.bool=!this.bool;
      var arr = this.tickBol;
      this.tickBol[index]=!this.tickBol[index];
      arr[index]=this.tickBol[index];
      this.tickBol=arr;
      var bol=false;
      for(var i=0;i<this.contentArr.length;i++){
        if (arr[i]) {
          bol=true;
        }   
      }
      this.AllBol=bol;
      this.jisuanFn();
    },
    //数量自加
    addFn:function(index){
      var mess=document.querySelectorAll(".mess");
      mess[index].innerHTML=Number(mess[index].innerHTML)+1;
      this.setbuyNum(Number(mess[index].innerHTML)+1);
      this.contentArr[index].bum =this.getbuyNum-1;
      this.contentArr[index].goodsPrice=(this.contentArr[index].originPrice)*(this.contentArr[index].bum)
      this.jisuanFn();
      
    },
    //数量自减
    jianFn:function(index){
      var mess=document.querySelectorAll(".mess");
      mess[index].innerHTML=Number(mess[index].innerHTML)-1;
      this.setbuyNum(Number(mess[index].innerHTML)+1);
      this.contentArr[index].bum =this.getbuyNum-1;
      this.contentArr[index].goodsPrice=(this.contentArr[index].originPrice)*(this.contentArr[index].bum);
      if(Number(mess[index].innerHTML)<1){
         mess[index].innerHTML=1
        this.contentArr[index].bum=1
        this.contentArr[index].goodsPrice=this.contentArr[index].originPrice
        }
        this.jisuanFn();
    },
    AllFn:function(){
      this.AllBol=!this.AllBol;
      var SelectArr=[];
      if (this.AllBol==false) {
        this.sumPrice=0;
        for(var i=0;i<this.contentArr.length;i++){
          SelectArr.push(false);
        }
        this.tickBol=SelectArr;
      }else {
        for(var i=0;i<this.contentArr.length;i++){
          SelectArr.push(true);
        }
        this.tickBol=SelectArr;
      }
      this.jisuanFn();
    },
    //结算
    SettlementFn(){
      var shopGoodsArr=[];
      for(var i=0;i<this.contentArr.length;i++){
        if (this.tickBol[i]==false) {
          shopGoodsArr.push(this.contentArr[i]);
          this.setShopArr(shopGoodsArr);
          this.$router.push("/confirm");
          this.contentArr.splice(i,1);
        }else {
          console.log('未选到商品')
        }  
      }
    }
  },
  mounted:function(){
    var tickBolArr = [];
    for(var i=0;i<this.contentArr.length;i++){
      tickBolArr.push(true);
      this.contentArr[i].goodsPrice=(this.contentArr[i].originPrice)*(this.contentArr[i].bum);
    }
    this.tickBol=tickBolArr;
  } 
}
</script>


<style lang='stylus' scoped>
  @import '../../assets/common/stylus/common.styl'
  .wrap1
    width pxtorem(750px)
    background-color #ebeae4
    min-height pxtorem(1334px)
    .head
      strong
        font-size pxtorem(32px)
        .back
          width pxtorem(20px)
          vertical-align middle
      .right
        font-size pxtorem(26px)
        display block
        vertical-align bottom
    .inner
      padding-top pxtorem(120px)
      .content
        width pxtorem(750px)
        height pxtorem(190px)
        padding-top pxtorem(20px)
        margin-top pxtorem(20px)
        background-color #fff
        position relative
        div
          display inline-block
          img
            display inline-block
            vertical-align middle
          img:nth-child(1)
            width pxtorem(34px)
            margin-left pxtorem(20px)
          img:nth-child(2)
            width pxtorem(34px)
            margin-left pxtorem(20px)
          img:nth-child(3)
            width pxtorem(170px)
            height pxtorem(170px)
        ul
          display inline-block
          vertical-align middle
          width pxtorem(480px)
          margin-left pxtorem(10px)
          li:nth-child(1)
            font-size pxtorem(26px)
            line-height pxtorem(42px)
            color: #1a1a1a
          li:nth-child(2)
            font-size pxtorem(24px)
            color #757575
            line-height pxtorem(72px)
          li:nth-child(3)
            font-size pxtorem(21px)
            line-height pxtorem(50px)
            span:nth-child(1)
              color #de0000
            div
              float right
              text-align center
              font-size pxtorem(32px)
              span:nth-child(1)
                display inline-block
                width pxtorem(50px)
                height pxtorem(50px)
                background-color #8dc33b
                border-radius pxtorem(10px)
                color #fff
                margin-right pxtorem(10px)
                font-size pxtorem(45px)
              span:nth-child(3)
                display inline-block
                width pxtorem(50px)
                height pxtorem(50px)
                background-color #8dc33b
                border-radius pxtorem(10px)
                color #fff
                margin-left pxtorem(10px)
                font-size pxtorem(45px)
          .del
            width pxtorem(160px)
            height 100%
            position absolute
            top 0
            right 0
            background-color rgba(225, 137, 70, 0.93)
            color #fff
            font-size pxtorem(45px)
            text-align center
            line-height pxtorem(210px)
            letter-spacing pxtorem(10px)
    .bottom
      width pxtorem(750px)
      margin auto
      height pxtorem(98px)
      background-color rgba(100, 10, 2, 0.5)
      position fixed
      bottom 0
      left 0
      color #fff
      ul
        display inline-block
        width pxtorem(750px)
        margin-left pxtorem(20px)
        font-size pxtorem(26px)
        vertical-align middle
        li
          float left
        li:nth-child(1)
          margin-top pxtorem(35px)
          img
            display inline-block
            width pxtorem(34px)
            vertical-align middle
            margin-right pxtorem(20px)
        li:nth-child(2)
          margin-left pxtorem(50px)
          span
            display inline-block
            margin-bottom pxtorem(10px) 
            line-height pxtorem(50px) 
          p
            font-size pxtorem(23px)
        li:nth-child(3)
          width pxtorem(255px)
          height pxtorem(98px)
          background-color #ee4147
          color #fff  
          font-size pxtorem(32px)
          text-align center
          line-height pxtorem(98px)
          float right        
</style>


