<template>
  <div class="">
   <myhead class="head">
    <strong slot='center' class="center">
       <img src="../../assets/Find/剑.png" alt="" class="sech" onclick="window.history.go(-1)">
        <span>首席评论榜</span>
      </strong>
    </myhead>
     <ul v-for="(item,index) in head" id="head">
        <li @click="fn(index)" class="head"><span class="span">{{item}}</span></li>
     </ul>
     <ul class="head_after"></ul>
      <ul v-for="(item,index) in Arr" class="contenr_first">
        <li><img v-bind:src="item.person"></li>
        <li>{{item.name}}</li> 
        <li>{{item.title1}}</li>
        <li class="blank"></li>
        <li>{{item.time}}</li> 
        <li @click="showhide(index)"><img v-bind:src="item.image1" ></li>
        <li><img v-bind:src="item.image2" @click="num(index)" v-show="hide[index]">
            <img v-bind:src="item.image3" @click="num(index)" v-show="!hide[index]"><strong class="number">{{item.goodnum}}</strong></li>
        <p v-show='fol'></p>
        <li class="clear"></li>
        <li v-show="show[index]">
             <input type="text" class="input" placeholder="评论："/>
             <button @click="submit(index)">发送</button>      
        </li>
        <li v-show='bol'></li>
      </ul> 
      
  </div>
</template>

<script>
import Head from '../Public/Header.vue'
import {mapGetters,mapActions} from "vuex"
// leftNrrow表示左边箭头箭头
export default {
   data(){
    return{
       bol:true,
       fol:true,
       show:[false,false,false,false],
       hide:[true,true,true,true],
       head:["转发人数10","评论20","赞25"],   
       
       Arr:[]

       
    }
   },
   methods:{
    fn:function(index){
        var heads=document.querySelectorAll(".head");
        var span=document.querySelectorAll(".span");
        this.Arr=[]
        console.log(index)
        for(var i=0;i<span.length;i++){
            span[i].style.borderBottom= 'none'
        }
        for(var i=0;i<this.peopleArr.length;i++){
        if(index==this.peopleArr[i].num){
            this.Arr.push(this.peopleArr[i])
           
            }
        }
        span[index] .style.borderBottom= '1px solid red'
    },
    submit:function(index){
          var input=document.querySelectorAll(".input")
          var blank=document.querySelectorAll(".blank")
          var language=input[index].value
          blank[index].innerHTML="星空之臣:"+language;
          input[index].value=""
    },
    showhide:function(index){
        this.bol=!this.bol
        this.show[index]=!this.show[index]
    },
    num:function(index){
         var number=document.querySelectorAll(".number")
         this.fol=!this.fol
          this.hide[index]=!this.hide[index]
           if(this.hide[index]==false){
               number[index].innerHTML=String(Number(number[index].innerHTML)+1)
             }else{
                number[index].innerHTML=String(Number(number[index].innerHTML)-1)
             }
      
         console.log(Number(number[index].innerHTML))  
    } 
   },
   computed:mapGetters({
      peopleArr:'getPeopleArr'
  }),
   components:{
      "myhead":Head
   },

    monted:function(){
        var show=[];
        var hide=[];
        for(var i=0;i<this.peopleArr.length;i++){
          show.push(false);
          hide.push(true);
        }
        this.show=show;
        this.hide=hide
    }
}
</script>

<style lang="stylus"  scoped>
@import '../../assets/common/stylus/common.styl'
.myhead
   height pxtorem(50px)
.sech
   position fixed
   left pxtorem(20px)
   top  pxtorem(23px)
   width pxtorem(32px)
 .center span
  font-size pxtorem(35px)  
#head
   width pxtorem(219.5px)
   height pxtorem(80px)
   float left
   margin-top pxtorem(130px)
   border-top pxtorem(3px) solid #eee
   line-height pxtorem(80px)
   border-bottom pxtorem(3px) solid #eee
   font-size pxtorem(28px)
#head:nth-of-type(2) 
  font-weight:bold 
  font-size pxtorem(31px)
#head:nth-of-type(3)
   width pxtorem(300px)
   text-align right

.contenr_first li
   pxtorem(750px)
   height pxtorem(130px)
.contenr_first>li:nth-of-type(1) img
   width pxtorem(100px)
   height pxtorem(100px)
   border-radius 50%
.head_after
   clear both
.contenr_first 
   position relative
   width pxtorem(750px)
   overflow hidden
   font-size pxtorem(23px)
   top pxtorem(15px)
   margin-bottom pxtorem(10px)
.contenr_first>li
   margin pxtorem(0px)
   padding pxtorem(0)   
.contenr_first>li:nth-of-type(1)
   position absolute
   top pxtorem(15px)
   left pxtorem(10px)
.contenr_first>li:nth-of-type(2)
   position absolute
   top pxtorem(15px)
   left pxtorem(130px)
   color gray
.contenr_first>li:nth-of-type(3)
   position absolute
   top pxtorem(50px)
   left pxtorem(130px)
   font-weight bolder
.contenr_first>li:nth-of-type(5)
   float left
   margin-top pxtorem(10px)
   margin-left pxtorem(130px)
   margin-bottom pxtorem(10px)
   height pxtorem(40px)
   width pxtorem(200px)
.contenr_first>li:nth-of-type(6) img,.contenr_first>li:nth-of-type(7) img
   width pxtorem(40px)
   height pxtorem(40)
   float left
   margin-top pxtorem(10px)
.contenr_first>li:nth-of-type(6) img
  margin-left pxtorem(260px)
.contenr_first>li:nth-of-type(7) img
   margin-left pxtorem(30px)
.contenr_first>li:nth-of-type(8)
  height pxtorem(2px)
  width pxtorem(600px)
  margin-left pxtorem(150px) 
  margin-top pxtorem(38px)
  background #eee    
.contenr_first>li:nth-of-type(9)
 width pxtorem(750px)
 height pxtorem(80px)
 background #e9eaec
 margin-top pxtorem(10px)
input:nth-of-type(1) 
 border 1px solid gray
 width pxtorem(450px)
 height pxtorem(50px)
 margin-top pxtorem(15px)
 margin-left pxtorem(100px)
 border-radius pxtorem(15px)
button 
 width pxtorem(100px)
 height pxtorem(50px)
 border-radius pxtorem(15px)
 margin-top pxtorem(15px)
.contenr_first>.blank
 width pxtorem(400px)
 margin-top pxtorem(90px)
 margin-bottom pxtorem(18px)
 margin-left pxtorem(130px)

.clear
 clear both
</style>
</style>



