import axios from 'axios';
export default{
	setCityName({commit,state},data){
		commit('SUB_ITEM',data);
	},
	setLoginMessage({commit,state},data){
		commit('GetLogin',data);
	},
	//设置到商品详情页面
	setShopCarGoods({commit,state},data){
		commit('GOODSPAGE',data);
	},
	//添加到购物车
	setShopCar({commit,state},data){
		commit('SHOPCARPAGE',data);
	},
	//确认订单SHOPARR
	setShopArr({commit,state},data){
		commit('SHOPARR',data);
	},
	setGoodsMessage({commit,state},data){
	},
	//是否为会员
	setHuiYuan({commit,state},data){
		commit('HUIYUAN',data)
	},

	setMenuFoodsArr({commit,state},data){
		commit('GetMenu',data);
	},
	setDetailAll({commit,state},data){
		commit('GetDetailAll',data);
	},
	 // 购物车叠加的数量
	setbuyNum({commit,state},data){
		commit('GetbuyNum',data);
	},
	setCicleArr({commit,state},data){
		commit('GetCicleArr',data);
	},
	setVideo({commit,state},data){
		commit('GetVideo',data);
	},
	setPeopleArr({commit,state},data){
		commit('GetPeopleArr',data);
	},
	setFindgoodsArr({commit,state},data){
		commit('GetFindgoodsArr',data);
	},
	setVegetable({commit,state},data){
		commit('GetVegetable',data);
	},
};


