<template>
	<div class="wrap">
		<div class="inner clear">
			<h3>抢购中</h3>
			<ul v-for='(item,index) in pincBuyArr' class="clear pincBuy" @click='pincBuyFn(index)'>
				<li>{{item.goodsName}}</li>
				<li class="clear">
					<count-down :endtime='item.endTime' class='countDown'></count-down>
					<strong>￥{{item.originPrice}}</strong></li>
				<li><img v-bind:src='item.src' alt=""></li>
			</ul>
		</div>
	</div>
</template>
<script>
import {mapGetters,mapActions} from "vuex"
import CountDown from '../Public/CountDown.vue'
// import axios from 'axios';
	export default {
	  data () {
	    return {
				timeBol:false
			}
		},
		components:{
			'count-down':CountDown
		},
		methods:{
			...mapActions([
      		"getpincBuyArr","setShopCarGoods"]),
			pincBuyFn:function(index){
      			var pincArr=[];
      			pincArr.push(this.$store.state.pincBuyArr[index])
      			this.setShopCarGoods(pincArr);
      			this.$router.push("/goodsdetail")
			}

		},
		//实时监听页面数据的变化
		computed:mapGetters({
      		pincBuyArr:'getpincBuyArr'

  		}),
		mounted:function(){
		}
	}
</script>
<style lang='stylus' scoped>
	@import '../../assets/common/stylus/common.styl'
	.wrap
		width 100%
		margin-top pxtorem(20px)
		background-color #FFF
		padding-top pxtorem(30px)
		.inner
			width pxtorem(710px)
			margin auto
			h3
				font-size pxtorem(32px)
				font-weight bolder
				letter-spacing pxtorem(2px)
				color #92c808
			.pincBuy
				width pxtorem(336px)
				float left
				margin pxtorem(9px)
				li:nth-child(1)
					color #b4b4b4
					font-size pxtorem(24px)
					line-height pxtorem(60px)
				li:nth-child(2)
					.countDown
						display inline-block
						width pxtorem(180px)
					strong
						float right
						margin-right pxtorem(10px)
						line-height pxtorem(60px)
						color red
				li:nth-child(3)
					img
						width 100%
						height pxtorem(270px)
						margin-top pxtorem(10px)
				
</style>


