
<template>
  <div class="All">
    <my-head class='head1'> 
      <strong slot='center' class="center">
        <img src="../../assets/Home/sech.png" alt="" class="sech">
        <input type="text" class="input" placeholder="搜索食材、菜品" v-on:input="Select()" >
      </strong>
      <!-- <strong slot='right'>
        <img src="../../assets/Home/news.png" alt="" class="news">
      </strong> -->
    </my-head >
     <my-hnav class="app">
     </my-hnav>    
 <!--中间部分的食品-->
    <ul class="Content" id="container" >
   	  <li>  
   	     <ul class="small_Content" v-for="(item,index) in Vegetablekind" @click="selectColor(index)" >
   	         <li><span>{{item.title1}}</span></li>
             <li><img v-lazy.container="item.url1"></li>
             <li><img v-lazy.container="item.url2" v-show="show[index]"></li>
             <li>￥{{item.goodsPrice}}</li>
             <li v-show="bol"></li>
         </ul>
       </li>  
    </ul>
    <!-- 搜png -->
    <ul v-show="substitute" class="substitute" >
            <li>搜索记录</li>
            <li class="first">
               <span>食材，菜料：</span>
               <span>西瓜</span>
               <span>木耳</span>
               <span>海带</span>
               <span>黄豆</span>
            <li class="caip">
               <span>菜品：</span>
               <span>红烧排骨</span>
               <span>辣子鸡</span>
            </li>
            <li class="hot">热门搜索</li>
             <li class="last">
              <span>粉皮如意卷</span>
              <span>竹简筒湘味小河虾</span>
              <span>鲜虾云吞</span>
              <span>湾仔红烧排骨</span>
            </li>

    </ul>
    <my-nav></my-nav>
  </div>
</template>

<script>
import Head from '../Public/Header.vue'
import Nav from '../Public/Nav.vue';
import Hnav from './Hnav.vue';
import Vue from 'vue';
import { Lazyload } from 'mint-ui';
import {mapGetters,mapActions} from "vuex"
Vue.use(Lazyload);
export default {
   data(){
    return{
         bol:true,
         substitute:true,
         Hveatable:[],
         Vegetablekind:[]
    }
   },
   methods:{
    Select:function(){
        this.Hveatable=[];
        this.Vegetablekind=[] 
        var input=document.querySelector("input");
        for(var i=0;i<this.Vegetable.length;i++){
            if(this.Vegetable[i].title1.indexOf(input.value)==-1){        
              }
              else if(input.value==""){  
              }
              else{
               this.Vegetablekind.push(this.Vegetable[i]);
               console.log(this.Hveatable)
              }
            }
            for(var i=0;i<this.goodsArr.length;i++){
            if(this.goodsArr[i].title2.indexOf(input.value)==-1){        
              }
              else if(input.value==""){  
              }
              else{
               this.Vegetablekind.push(this.Vegetable[i]);
               console.log(this.Hveatable)
              }
            }
             
          if(this.Vegetablekind.length!=0){
             this.substitute=false
          }
          else{
             this.substitute=true
          }

     },
     selectColor:function(index){
       this.show[index]=!this.show[index]
       this.bol=!this.bol
       var ul=document.querySelectorAll(".small_Content");
       if(this.show[index]==true){
        ul[index].style.background = 'green'
      }else{
         ul[index].style.background = 'white'
      }
       
       console.log('aa')
     }
   },
    computed:mapGetters({
      goodsArr:'getFindgoodsArr',
      Vegetable:'getVegetable'
  }),
   components:{
    "my-head":Head,
    'my-nav':Nav,
     'my-hnav':Hnav
   },
   mounted:function(){
    var showw = [];
    for(var i=0;i<this.Vegetablekind.length;i++){
      showw.push(false)
    }
    for(var i=0;i<this.goodsArr.length;i++){
      showw.push(false)
    }
    this.show=showw;
   }

}
</script>

<style lang="stylus" scoped>
@import '../../assets/common/stylus/common.styl'
.All
    width pxtorem(750px)
    background #ebeae4
    .head1
        strong
          .down
            width pxtorem(23px)
          .input
            background-color #65d600
            color #fff
            width pxtorem(390px)
            height pxtorem(50px)
            border-radius pxtorem(20px)
            margin-left pxtorem(-55px)
            outline none
            padding-left pxtorem(60px)
            font-size pxtorem(26px)
            margin-right pxtorem(80px)
          .news
            vertical-align middle 
            width pxtorem(40px)
        .center
          position relative
          .sech
            position absolute
            top pxtorem(3px)
            left pxtorem(-35px)
            width pxtorem(30px)
            z-index 10 
 .head
     width pxtorem(750px)
     
     
     margin-top  pxtorem(120px)
 .head:after  
     display  table
     content  " "
     clear  both
  
 .head_child
     float left
     width pxtorem(150px)
     height  pxtorem(210px)
     border pxtorem(1px) solid white
     margin-left pxtorem(12px)
     background  white
     border-radius  pxtorem(12px)
     margin-top pxtorem(30px)
     
.head_child li:nth-of-type(1)
     width  pxtorem(150px)
     height  pxtorem(45px)  
     line-height  pxtorem(45px) 
     border-bottom pxtorem(1px) solid #ebeae4 
     text-align center
.head_child li img
      width pxtorem(120px)
      margin-top  pxtorem(50px)
      margin-left  pxtorem(10px)
.Content
      width pxtorem(750px)
      height  pxtorem(1200px)
      margin-top pxtorem(120px)
      
.Content li ul
      width pxtorem(222px)
      height  pxtorem(330px)
      background white
      border-radius  pxtorem(10px)
      float left
      margin-left pxtorem(20px)
      margin-top pxtorem(20px)
.substitute
     position absolute 
     top pxtorem(100px)
     left pxtorem(50px)  
     width pxtorem(650px)

          
.substitute>li:nth-of-type(1),.substitute>li:nth-of-type(4)
         color red
         width 100%
         height pxtorem(100px)
         line-height pxtorem(150px)
         font-size pxtorem(30px)
         border-bottom pxtorem(2px) green solid

.first>span
         display inline-block
         width pxtorem(200px)
         font-size pxtorem(30px)
         margin-top pxtorem(15px)
         margin-bottom pxtorem(15px)
.caip>span
         display inline-block  
         width pxtorem(200px)
         font-size pxtorem(30px)
         margin-top pxtorem(15px)
         margin-bottom pxtorem(15px)
.caip>span:nth-of-type(3)
        margin-left pxtorem(100px)                  
.first>span:nth-of-type(1),.caip>span:nth-of-type(1)
         display block
         font-weight bolder
         font-size pxtorem(28px)
         margin-top pxtorem(30px)
.first>span:nth-of-type(3),.first>span:nth-of-type(5)
         margin-left pxtorem(100px)      
.hot 
        margin-top pxtorem(10px)        
.last>span
       display inline-block 
       width pxtorem(300px)
       margin-top pxtorem(25px)
       font-size pxtorem(30)
       margin-bottom pxtorem(10px)
      
.last>span:nth-of-type(2),.last>span:nth-of-type(4)
        
.small_Content
      position relative      
.small_Content li:nth-of-type(1)
      width pxtorem(222px)
      height  pxtorem(70px)
      line-height  pxtorem(70px) 
      font-size pxtorem(28px)
      border-bottom  1px solid #ebeae4 
.small_Content li:nth-of-type(1)>span
      margin-left pxtorem(40px) 
      width pxtorem(100px)
      text-align right 
      display inline-block
             
.small_Content li:nth-of-type(2)>img
      width pxtorem(160px)
      margin-top pxtorem(50px)
      margin-left  pxtorem(30px)
.small_Content li:nth-of-type(3)
      position absolute
      top pxtorem(10px)
      left pxtorem(10px)      
.small_Content li:nth-of-type(3)>img
      width pxtorem(35px)
.small_Content li:nth-of-type(4)
       position absolute 
       top pxtorem(25px)
       left pxtorem(150px)
       color red
            
.p 
     color red 
     font-size pxtorem(30px)
     margin-top pxtorem(200px) 
 .app
    position absolute
    top   pxtorem(30px)
    left  pxtorem(540px)
    margin-left pxtorem(130px)
    z-index 12 
  
  
      
</style>



