<template>
  <div class="nav">
    <ul>
      <router-link v-for='(item,index) in navArr' tag="li" v-bind:to='item.url' @click.native='navFn(index)' class='navFn'>
        <img v-bind:src='item.src' class="navImg">
        {{item.title}}
      </router-link>
      
    </ul>
  </div>
</template>
<script>
// import axios from 'axios';
import {mapGetters,mapActions} from "vuex";
export default {
  data () {
    return {

    }
  },
  //实时监听页面数据的变化
  computed:mapGetters({
    navArr:'getnavArr'
  }),
  methods:{
    ...mapActions([
    "getnavArr"]),
    navFn:function(index){
      this.$nextTick(function(){
         this.changeFn($(".navFn"),$(".navImg"),index);
      })
    },
    //点击改变路径
    changeFn:function(ele1,ele2,index){
      ele1.eq(index).css({"color":"#d4237a"});
      ele2.eq(index).get(0).src=this.navArr[index].src1;
    }
  },
  mounted:function(){
    switch (this.$route.fullPath) {
      case "/":this.changeFn($(".navFn"),$(".navImg"),0);break;
      case "/select":this.changeFn($(".navFn"),$(".navImg"),1);break;
      case "/find":this.changeFn($(".navFn"),$(".navImg"),2);break;
      case "/my":this.changeFn($(".navFn"),$(".navImg"),3);break;
      default:break;
    }
  }
}
</script>
<style lang='stylus' scoped>
  @import '../../assets/common/stylus/common.styl'
  .nav
    position fixed
    bottom 0
    left 0
    width 100%
    border-top pxtorem(1px) solid #ccc
    background-color #65d600
    height pxtorem(98px)
    ul
      display flex
      li
        text-align center
        flex 1
        line-height pxtorem(40px)
        font-size pxtorem(20px)
        color#808080
        img
          display block
          margin auto
          height pxtorem(43px)
          margin-top pxtorem(14px)
</style>





