<template>
  <div class="wrap">
    <div class="inner clear" @click="myorderFn">
      <span>全部订单</span>
      <img src="../../assets/My/left.png" alt="">
    </div>
    <ul class="order">
      <router-link v-for='(item,index) in orderArr' tag="li" v-bind:to='item.src'>
        <img v-bind:src='item.url'>
        {{item.title}}
      </router-link>
    </ul>
    <ul class="set">
      <router-link v-for='(item,index) in setArr' tag="li" v-bind:to='item.detail'>
        <img v-bind:src='item.url'>
        {{item.title}}
      </router-link>
    </ul>
  </div>
</template>

<script>
import {mapGetters,mapActions} from "vuex"
export default {
  data () {
    return {
    }
  },
  //2.注册组件
  components:{

  },

  created:function(){
    
  },
  //实时监听页面数据的变化
  computed:{
    ...mapGetters({
      orderArr:"getMyPage",
      setArr:"getSetArr"
    })

  },
  methods:{
    ...mapGetters({
      orderArr:"getMyPage",
      setArr:"getSetArr"
    }),
    myorderFn:function(){
      // this.$router.push('/myorderFn')
      console.log('123')
    }
  }
  
}
</script>


<style lang='stylus' scoped>
  @import '../../assets/common/stylus/common.styl'
  .wrap
    width pxtorem(750px)
    padding-bottom pxtorem(98px)
    .inner
      width pxtorem(710px)
      margin auto
      height pxtorem(70px)
      line-height pxtorem(70px)
      border-bottom pxtorem(1px) solid #ebeae4
      img
        float right
        width pxtorem(15px)
        margin-top pxtorem(20px) 
    .order
      text-align center
      margin pxtorem(20px) 0
      color #7e7e7e
      li
        display inline-block
        width pxtorem(110px)
        margin 0 pxtorem(30px)
        img
          width pxtorem(90px)
    .set
      width pxtorem(750px)
      li:last-child
        border-bottom pxtorem(1px) solid #ebeae4
      li
        border-top pxtorem(1px) solid #ebeae4
        line-height pxtorem(70px)
        padding 0 pxtorem(20px)
        img
          display inline-block
          width pxtorem(40px)
          vertical-align middle
          margin-right pxtorem(10px)
</style>


