const SUB_ITEM = "SUB_ITEM";//城市定位
const GetLogin = 'GetLogin';//登录信息
const GOODCOUNT = 'GOODCOUNT';//商品详情页面
const CHOICE = 'CHOICE';
const FOODIE = 'FOODIE';
const GOODSPAGE = 'GOODSPAGE';//商品详情
const SCORLLARR = 'SCORLLARR';
const SUSPENARR = 'SUSPENARR';
const EVALUATEARR = 'EVALUATEARR';
const EVALUATELIST = 'EVALUATELIST';
const SHOPCARPAGE = 'SHOPCARPAGE';//购物车信息
const SHOPARR = 'SHOPARR'//确认订单页面
const SETTLEMENT = "SETTLEMENT";
const ORDERARR = 'ORDERARR';
const SETARR = 'SETARR';
const ORDERENTERARR = 'ORDERENTERARR';
const GOODSMESSAGE = 'GOODSMESSAGE';//请求API接口
const HUIYUAN = 'HUIYUAN';//是否为会员

const GetMenu='GetMenu';
const GetDetailAll='GetDetailAll';
const GetbuyNum='GetbuyNum'
const GetCicleArr='GetCicleArr';
const GetVideo='GetVideo';
const GetPeopleArr='GetPeopleArr';
const GetFindgoodsArr='GetFindgoodsArr';
const GetVegetable='GetVegetable';


export default{
	//城市定位
	[SUB_ITEM] (state,data){
		state.city=data
	},
	//登录信息
	[GetLogin](state,data){
		state.LoginMessage=data
	},
	// 购物车叠加的数量
   [GetbuyNum](state,data){
		state.buyNum=data

	},

	[CHOICE](state,data){
		state.choice=data
	},
	[FOODIE](state,data){
		state.foodie=data
	},
	[GOODSPAGE](state,data){//商品详情
		state.goodspage=data;
	},
	[SCORLLARR](state,data){
		state.scorllArr=data
	},
	[SUSPENARR](state,data){
		state.suspenArr=data
	},
	[EVALUATEARR](state,data){
		state.evaluateArr=data
	},
	[EVALUATELIST](state,data){
		state.evaluateList=data
	},
	[SHOPCARPAGE](state,data){//加入购物车shopcarpage
		// var mes = ;
		// var shopArr=[];
		// shopArr.push(mes);
		state.shopcarpage.push(data[0]);
		// state.shopcarpage=data
	},
	[SHOPARR](state,data){//加入购物车shopcarpage
		state.shopArr=data
	},
	[SETTLEMENT](state,data){
		state.settlement=data
	},
	[ORDERARR](state,data){
		state.orderArr=data
	},
	[SETARR](state,data){
		state.setArr=data
	},
	[ORDERENTERARR](state,data){
		state.orderEnterArr=data
	},
	[GOODSMESSAGE](state,data){
		state.goodsMessage=data
	},
	[HUIYUAN](state,data){
		state.text = data
	},

	[GetMenu](state,data){
		state.foodsArr=data
	},
	[GetDetailAll](state,data){
		state.DetailAll=data
	},
	[GetCicleArr](state,data){
		state.CicleArr=data
	},
	[GetVideo](state,data){
		state.GetVideo=data
	},
	[GetPeopleArr](state,data){
		state.GetPeopleArr=data
	},
	[GetFindgoodsArr](state,data){
		state.GetFindgoodsArr=data
	},
	[GetVegetable](state,data){
		state.GetVegetable=data
	}

	
};


