<template>
  <div class="wrap1">
    <my-head class='head'> 
      <strong slot='left'>
        <img src="../../assets/My/right.png" alt="" class="back" @click="backFn">
      </strong>
      <strong slot='center'>
        快速帮助中心
      </strong>
    </my-head>
    <ul>
      <li v-for='(item,index) in setArr' v-bind:to='item.detail' @click="helpFn(index)" class="oLi">
        <img v-bind:src='item.url'>
        {{item.title}}
        <img v-bind:src='item.url1' alt="" class="down" v-show="viewbol[index]">
        <img v-bind:src='item.url2' alt="" class="up" v-show="!viewbol[index]">
        <p class="txt" v-show='bol1[index]'>{{item.text}}</p>
        <p v-show='bol'></p>
      </li>

    </ul>
  </div>
</template>

<script>
import Head from '../Public/Header.vue'
// import axios from 'axios';
import {mapGetters,mapActions} from "vuex";
export default {
  data () {
    return {
      bol:false,
      bol1:[false,false,false,false],
      viewbol:[true,true,true,true]
    }
  },
  //2.注册组件
  components:{
    "my-head":Head
  },

  created:function(){
    
  },
  //实时监听页面数据的变化
  computed:mapGetters({
    setArr:'getHelpArrPage'
  }),
  methods:{
    ...mapActions([
    "getHelpArrPage"]),
    backFn:function(){
      this.$router.push("/set")
    },
    helpFn:function(index){
      this.viewbol[index]=!this.viewbol[index];
      this.bol1[index] =!this.bol1[index];
      this.bol =!this.bol;
    }
  }
  
}
</script>


<style lang='stylus' scoped>
  @import '../../assets/common/stylus/common.styl'
  .wrap1
    width pxtorem(750px)
    .head
      strong
        font-size pxtorem(32px)
        .back
          width pxtorem(20px)
          vertical-align middle
    ul
      width pxtorem(750px)
      background-color #ebeae4
      padding-top pxtorem(140px)
      li
        width pxtorem(750px)
        min-height pxtorem(79px)
        border-bottom pxtorem(1px) solid #c5c4c4
        background-color: #FFF
        line-height pxtorem(80px)
        font-size pxtorem(26px)
        .txt
          background-color: #ebeae4
          // border-top pxtorem(1px) solid #c5c4c4
          // border-bottom pxtorem(1px) solid #c5c4c4
          padding 0 pxtorem(20px)
          line-height pxtorem(65px)
        img:nth-child(1)
          width pxtorem(40px)
          vertical-align middle
          margin-left pxtorem(20px)
          margin-right pxtorem(20px)
        img:nth-child(2)
          width pxtorem(25px) 
          float right
          vertical-align middle
          margin-right pxtorem(20px)
          margin-top pxtorem(35px)
        img:nth-child(3)
          width pxtorem(25px) 
          float right
          vertical-align middle
          margin-right pxtorem(20px)
          margin-top pxtorem(35px)
          
        
</style>


