<template>
  <div id="detail">
    <div v-for='goods in goodsArr'>
       <my-head class='head'> 
        <strong slot='left'>
          <img src="../../assets/My/right.png" alt="" class="back" @click="backFn">
        </strong>
        <strong slot='center'>
          {{goods.title}}
        </strong>
        <strong slot='right' class="right" @click='selectFn'>
          <ul>
            <li></li>
            <li></li>
            <li></li>
          </ul>
          <span class="select">
            <a href="###" v-for='(item,index) in selectArr' @click='selectBackFn(index)'>{{item}}</a>
          </span>
        </strong>
      </my-head>
      <div class='bigPic'><img :src="goods.src" alt="" @click="Img_scaseFn" class="scase"></div>
      <div class="message">
        <dl>{{goods.goodsName}}
          <dt>{{goods.goodsEffect}}
            <dd>￥{{goods.goodsPrice}}</dd>
          </dt>
        </dl>
        <div class="content1_inner">
          <div>
            <a href="###" @click='collectFn'>
              <img src="../../assets/Home/collect-b.png" alt="" v-show='collectBol'>
              <img src="../../assets/Home/collect.png" alt="" v-show='!collectBol'>收藏</a><span></span>
            <a href="###" @click='shareFn'>
              <img src="../../assets/Home/share-b.png" alt="" v-show='shareBol'>
              <img src="../../assets/Home/share.png" alt="" v-show='!shareBol'>分享</a>
            <div class="suspen">
              <p>分享到：</p>
              <a href="###" v-for='(item,index) in suspenArr' @click='friendShareFn(index)'><img v-bind:src="item.url" alt="">{{item.txt}}</a>
            </div>
          </div>
          <div>周销{{goods.goodsNum}}份</div>
        </div>
      </div>
      <div class="specifications">
        <p>规格：1000g</p>
        <p>{{goods.specifications}}</p>
      </div>
    </div>
   
    <div class="pay">
      <div>16:00前付款，预计3月23日送达</div>
      <div>
        <span><img src="../../assets/Home/clockgray.png" alt="">信用卡支付</span>
        <span><img src="../../assets/Home/clockgray.png" alt="">支付宝支付</span>
        <span><img src="../../assets/Home/clockgray.png" alt="">微信支付</span>
        <span><img src="../../assets/Home/clockgray.png" alt="">7天无理由</span>
      </div>
    </div>
    <div class="goods">
      <span @click='goodsDetailFn'>商品详情</span>
      <span @click='evaluateFn'>宝贝评价</span>
    </div>
    <div class="evaluate" v-show='evaluateBol'>
      <p>宝贝评价({{evaluateNum}})</p>
      <ul class="talk">
        <li>
          <div><img v-bind:src="evaluateArr[0].url" alt="">{{evaluateArr[0].name}}</div>
          <p>{{evaluateArr[0].talk}}</p>
          <div>{{evaluateArr[0].time}}</div>
        </li>
        <li v-for="item in evaluateList" v-show='listBol'>
          <div><img v-bind:src="item.url" alt="">{{item.name}}</div>
          <p>{{item.talk}}</p>
          <div>{{item.time}}</div>
        </li>
      </ul>
      <div class="seeAll" @click='seeAllFn'>{{mes}}</div>
    </div>
    <div class="goodsDetail" v-show='!evaluateBol' v-for='Img in DetailImgArr'>
      <img :src="Img" alt="">
    </div>
    <div class="bottom">
      <span @click="shopcarFn">加入购物车</span>
      <span @click="payNowFn">立马结算</span>
    </div>
  </div>
</template>

<script>
import Head from '../Public/Header.vue'
import Swiper from '../Public/Swiper.vue'
import { Toast } from 'mint-ui'
// import axios from 'axios';
import {mapGetters,mapActions} from "vuex";
export default {
  data () {
    return {
      selectArr:["首页","帮助","客服信息"],
      DetailImgArr:[require("../../assets/Home/Img1.jpg"),require("../../assets/Home/Img2.jpg"),require("../../assets/Home/Img3.jpg"),require("../../assets/Home/Img4.jpg"),require("../../assets/Home/Img5.jpg"),require("../../assets/Home/Img6.jpg"),require("../../assets/Home/Img7.jpg"),require("../../assets/Home/Img8.jpg")],
      listBol:false,
      mes:'查看所有评价',
      evaluateNum:"",
      collectBol:true,
      shareBol:true,
      slideBol:true,
      evaluateBol:false,
      goods:"",
      addshopcar:0,
      addsBol:true,
      noCar:false
    }
  },
  //2.注册组件
  components:{
    "my-head":Head,
    "my-swiper":Swiper
  },

  created:function(){
    
  },
  computed:mapGetters({
    goodsArr:'getGoodsPage',
    contentArr:"getScorllArr",
    suspenArr:"getSuspenArr",
    evaluateArr:"getEvaluateArr",
    evaluateList:"getEvaluateList",
    shopCount:"getshopCount"

  }),
  methods:{
    ...mapActions([
    "getGoodsPage",
    "getScorllArr",
    "getSuspenArr",
    "getEvaluateArr",
    "getEvaluateList",
    "setShopCarGoods",
    "setShopCar",//
    "setCarGoodCount",//设置购物车商品数量
    "getshopCount"//获取购物车商品数量
    ]),
    backFn:function(){
      this.$router.go(-1);
      if (this.noCar) {
        if (this.addsBol) {
          this.goodsArr[0].bum=this.addshopcar;
          this.setShopCar(this.goodsArr);
          this.addsBol=false;

        }
      }
    },
    Img_scaseFn:function(){
      $(".scase").stop().animate({"width":"120%","height":"130%","marginLeft":"-10%","marginTop":"-15%"},1000,function(){
        $(".scase").stop().animate({"width":"100%","height":"100%","marginLeft":0,"marginTop":0},800)
      })
    },//加入购物车
    shopcarFn:function(){
      Toast({
          message: '已经成功加入购物车'+"x"+(this.addshopcar+1),
          duration:1000
        });
      this.noCar=true;
      this.addshopcar++; 
    },
    payNowFn:function(){
      console.log('结算')
    },
    selectFn:function(){
      this.slideBol=!this.slideBol;
      if (this.slideBol==false) {
        $(".select").slideDown();
      }else {
        $(".select").slideUp();
      }    
    },
    selectBackFn:function(index){
      switch (index) {
        case 0:
        this.$router.push("/");
        if (this.addsBol) {
          this.goodsArr[0].bum=this.addshopcar;
          this.setShopCar(this.goodsArr);
          this.addsBol=false;
        }
        break;
        case 1:this.$router.push("/help");break;
        case 2:this.$router.push("/my");break;
        default:break;
      }
    },
    collectFn:function(){
      if (this.collectBol==false) {
        Toast({
          message: '已经收藏',
          iconClass: 'icon icon-success',
          duration:1300
        });
        return;
      }else {
        this.collectBol=false;
         Toast({
          message: '收藏成功',
          iconClass: 'icon icon-success',
          duration:1300
        });
      }

    },
    shareFn:function(){
      $(".suspen").stop().animate({right:0},800);
    },
    friendShareFn:function(index){
      $(".suspen").stop().animate({right:'-510px'},1000);
      this.shareBol=false;
      var value = this.suspenArr[index].txt;
      Toast({
        message: "已经分享到"+value,
        iconClass: 'icon icon-success',
        duration:1300
      });
    },
    seeAllFn:function(){
      if (this.listBol) {
        this.listBol=false;
        this.mes="查看所有评价";
      }else {
        this.listBol=true;
        this.mes="收起评价";
      }
    },
    goodsDetailFn:function(){
      $(".goods>span").css("backgroundColor","#ebeae4")
      $('.goods>span:eq(0)').css("backgroundColor","#eecb91")
      this.evaluateBol=false;
      this.listBol=false;
    },
    evaluateFn:function(){
      $(".goods>span").css("backgroundColor","#ebeae4")
      $('.goods>span:eq(1)').css("backgroundColor","#eecb91");
      this.evaluateBol=true;
    }
  },
  mounted:function(){
    this.evaluateNum=this.evaluateList.length;
  } 
  
}
</script>


<style lang='stylus' scoped>
  @import '../../assets/common/stylus/common.styl'
  #detail
    width pxtorem(750px)
    padding-bottom pxtorem(100px)
    overflow hidden
    .head
      strong
        font-size pxtorem(32px)
        .back
          width pxtorem(20px)
          vertical-align middle
      .right
        ul
          li
            width pxtorem(10px)
            height pxtorem(10px)
            border pxtorem(2px) solid #fff
            border-radius 50%
            float left
            margin-top pxtorem(60px)
            margin-left pxtorem(5px)
      strong:nth-child(3)
        position relative
      .select
        position absolute
        top pxtorem(120px)
        right pxtorem(-20px)
        width pxtorem(200px)
        height auto
        background-color rgba(113, 215, 38, .75)
        z-index 500
        display none
        a
          display block
          color #fff
          width 100%
          height pxtorem(60px)
          line-height pxtorem(60px)
          border-bottom pxtorem(2px) solid #fff
        
  .bigPic
    padding-top pxtorem(100px)
    height pxtorem(500px)
    overflow hidden
    img
      width 100%
      height 100%
  .message
    border-bottom pxtorem(1px) solid #7d7d7d
    font-size pxtorem(26px)
    width pxtorem(710px)
    padding pxtorem(20px)
    dl
      display inline-block
      width pxtorem(520px)
      font-weight bold
      color #7d7d7d
      dt
        line-height pxtorem(80px)
        font-size pxtorem(24px)
        dd
          line-height pxtorem(40px)
    .content1_inner
      float right
      text-align right
      div
        a
          display inline-block
          width pxtorem(70px)
          height pxtorem(70px)
          line-height pxtorem(40px)
          img
            width pxtorem(50px)
        span
          display inline-block
          width pxtorem(1px)
          height pxtorem(80px)
          background-color: #7d7d7d
          margin-left pxtorem(20px)
      div:nth-child(2)
        margin-top pxtorem(30px)
      .suspen
        position fixed
        right pxtorem(-210px) 
        top 45%
        width pxtorem(190px)
        height pxtorem(300px)
        background-color: rgba(0, 0, 0, .75)
        border-radius pxtorem(30px) 0 0 pxtorem(30px)
        padding-left pxtorem(20px)
        p
          color #fff
          text-align left
          padding-top pxtorem(20px)
        a
          color #fff
          display block
          width pxtorem(180px)
          text-align left
          height pxtorem(50px)
          line-height pxtorem(50px)
          margin-top pxtorem(10px)
          img
            width pxtorem(40px)
            vertical-align middle
            margin-right pxtorem(10px)
  .specifications
    padding pxtorem(20px)
    border-bottom pxtorem(1px) solid #7d7d7d
    p
      line-height pxtorem(40px)
      font-size pxtorem(24px)
      color #8d8d8d
    p:nth-child(2)
      text-indent pxtorem(48px)
  .pay
    div
      width pxtorem(710px)
      padding 0 pxtorem(20px)
      height pxtorem(56px)
      border-bottom pxtorem(1px) solid #7d7d7d
      background-color: #ebeae4
      line-height pxtorem(56px)
      color #646464
    div:nth-child(2)
      text-align center
      span
        img
          vertical-align middle
          width pxtorem(28px)
          margin-right pxtorem(10px)
      span:nth-child(2)
        margin 0 pxtorem(20px)
      span:nth-child(3)
        margin 0 pxtorem(20px)
  .goods
    width pxtorem(750px)
    margin-top pxtorem(30px)
    span
      float left
      width pxtorem(375px)
      height pxtorem(80px) 
      line-height pxtorem(80px)
      text-align center
    span:nth-child(1)
      background-color #eecb91
    span:nth-child(2)
      background-color #ebeae4
  .evaluate
    width pxtorem(750px)
    .seeAll
      width pxtorem(200px)
      height pxtorem(40px)
      border pxtorem(1px) solid red
      border-radius pxtorem(20px)
      text-align center
      line-height pxtorem(40px)
      margin pxtorem(20px) auto
    p:nth-child(1)
      line-height pxtorem(60px)
      padding-left pxtorem(10px)
    .talk
      margin-top pxtorem(10px)
      li
        border-bottom pxtorem(1px) solid #7d7d7d
        padding pxtorem(20px)
        p
          font-size font-size pxtorem(26px)
          text-indent pxtorem(52px)
          line-height pxtorem(40px)
        div:nth-child(1)
          font-size pxtorem(24px)
          img
            display inline-block
            vertical-align middle
            width pxtorem(35px)
            margin-right pxtorem(5px)
        div:nth-child(3)
          color #8d8d8d
  .bottom
    width pxtorem(750px)
    height pxtorem(100px)
    position fixed
    bottom 0
    left 0
    span
      float left
      width 50%
      height pxtorem(100px)
      background-color: #8dc33b
      text-align center
      line-height pxtorem(100px)
      color #fff
      font-size pxtorem(40px)
    span:nth-child(2) 
      background-color: #e44a30
  .goodsDetail
    width 100%
    img
      width 100%
</style>


