<template>
  <div class="content1">
    <mt-swipe :auto="3000">
      <mt-swipe-item v-for='item in scrol'><img v-bind:src="item.url" alt=""></mt-swipe-item>
    </mt-swipe>
  </div>
</template>
<script>
  import Vue from "vue";
  import { Swipe, SwipeItem } from 'mint-ui';
  
  //注册组件
  Vue.component(Swipe.name, Swipe);
  Vue.component(SwipeItem.name, SwipeItem);
  import 'mint-ui/lib/style.css';
  export default {
    props:["scrol"],
    data () {
      return {
       
      }
    }
  }
</script>

<style lang='stylus' scoped>
  @import '../../assets/common/stylus/common.styl'
  .content1
    width 100%
    position relative
    height pxtorem(350px)
    margin-top pxtorem(120px)
    img
      position: absolute
      top 0
      left 0
      width 100%
</style>



