<template>
  <div class="">
     <ul>
       <li class="head">
         <img v-bind:src="bigUrl">
       </li>

       <li class="headContent">
         <router-link :to="{path: './find'}"><img v-bind:src="leftNrrow" class="headContent_imgOne"></router-link>
         <img v-bind:src="heart" class="headContent_imgTwo">
       </li>
       <li class="headtext">
         <span>赞赏</span>
         <span>宝宝别闹啦，咬你 其实特别爱你</span>
        
       </li>
       <li class="headfoot">
         <img v-bind:src="smallUlr">
       </li>
       
       <li class="Content">
        <hr/>
         <p>鼓励一下，期待您更有的作品</p>
         <div class="monydiv" v-for="(item,index) in  money">
         <span @click="Fmoney(index)"><strong>{{item.num}}</strong>元</span>
         </div>
         <button  @click="fn">自定义金额</button>
         
       </li>
      <li class="last">
         <img v-bind:src="gou">
         <span class="niname">匿名赞赏</span>
      </li>
       <li class="hide" v-show="show">
         <span @click="blok">X</span>
         <span>自定义金额</span>
          <input type="text" placeholder="金额(元)可输入1-200">
          <button @click="blok">赞赏</button>
       </li>
       <li class="hide_big" v-show="h_show"></li>
     </ul>
      
  </div>
</template>

<script>
// leftNrrow表示左边箭头箭头
import { Toast } from 'mint-ui'
export default {
   data(){
    return{
        bigUrl:require("../../assets/Find/番茄2.png"),
        smallUlr:require("../../assets/Find/小番茄.png"),
        leftNrrow:require("../../assets/Find/剑.png"),
        heart:require("../../assets/Find/heart.png"),
        gou:require("../../assets/Find/gou.png"),
        show:false,
        h_show:false,
        money:[
         {num:1},{num:2},{num:8},{num:18},{ num:28},{num:38}
         ]
    }
   
   },
   methods:{
    fn:function(){
        this.show=!this.show,
        this.h_show=!this.h_show
    },
    blok:function(){
      this.show=!this.show,
        this.h_show=!this.h_show
        Toast({
          message: '赞赏成功',
          iconClass: 'icon icon-success',
          duration:1300
        });
      
    },
    Fmoney:function(index){
      var i=this.money[index].num
      Toast({
          message: '您成功的赞赏'+i+"元",
          iconClass: 'icon icon-success',
          duration:1300
        });
    }
   },
   components:{
   
   }
}
</script>

<style lang="stylus"  scoped>
@import '../../assets/common/stylus/common.styl'
.head 
   background #eae9e4
   height pxtorem(473px)
   width pxtorem(750px)
   
.head img
   width 100%
   height pxtorem(473px)
.headContent_imgOne
   position absolute
   top pxtorem(30px)
   left pxtorem(40px)
   width pxtorem(20px)
   height pxtorem(39px)
.headContent_imgTwo
   position absolute
   top pxtorem(240px)
   left pxtorem(263px)   
.headtext span:nth-of-type(1)
   position absolute
   top pxtorem(39px)
   left pxtorem(342px)
   font-size pxtorem(34px)
.headtext span:nth-of-type(2)
   text-align right 
   position absolute
   top pxtorem(200px)
   width pxtorem(240px)
   font-size pxtorem(30px)
   left pxtorem(258px)
   line-height pxtorem(35px)   
.headfoot
   width pxtorem(210px)
   height pxtorem(210px)
   border-radius 50%
   position absolute
   top pxtorem(375px)
   left pxtorem(276px)
   background white
.headfoot img
  width  pxtorem(180px)
  height pxtorem(180px)
  margin-top pxtorem(6px) 
  margin-left  pxtorem(16px)
 .Content
   width pxtorem(650px) 
   height  pxtorem(500px)
   margin-left pxtorem(50px)
   margin-top  pxtorem(150px)
 .Content p
   width  pxtorem(518px)  
   height  pxtorem(38px)
   font-size  pxtorem(34px)
   margin-top pxtorem(-34px)
   margin-left pxtorem(66px)
   background white
.Content .monydiv
   display inline-block
.Content .monydiv>span
   display inline-block
   width  pxtorem(181px)
   height pxtorem(137px)
   border pxtorem(5px) solid #6ab439
   border-radius pxtorem(15px)
   margin-top pxtorem(20px)
   line-height pxtorem(137px)
   text-align center
.Content .monydiv span strong
  font-size pxtorem(40px)
.Content .monydiv span:nth-of-type(2),.Content span:nth-of-type(5)
   margin-left pxtorem(20px)
   margin-right  pxtorem(20px)
.Content .monydiv span:nth-of-type(1),.Content span:nth-of-type(4)
   margin-left pxtorem(10px)  
 .Content button
   margin-top  pxtorem(40px)
   margin-left pxtorem(240px)
   width pxtorem(182px)
   height pxtorem(60px)
   border-radius pxtorem(25px)
   background #c3ad88
   color white
   font-size pxtorem(25px)
  .last
    height pxtorem(40px)
    width pxtorem(200px) 
    margin-top pxtorem(120px)
    margin-left pxtorem(303px)
    position relative
 .last img
    height pxtorem(30px)
    width pxtorem(30px) 
 .last span
    font-size  pxtorem(23px) 
    position absolute
    top  2px
    left pxtorem(52px) 
  .hide
    width pxtorem(650px)
    height pxtorem(397px)  
    position absolute
    top pxtorem(305px)
    left pxtorem(50px)
    background white
    text-align left
    overflow hidden
    color white 
     border pxtorem(2px) solid white
    border-radius pxtorem(30px)
    z-index 10
  .hide  span 
     display block  
     margin-top pxtorem(40px)
     float left 
  .hide  span:nth-of-type(1)
    margin-left  pxtorem(31px)
    font-size pxtorem(30px)
    color #87868f
 .hide  span:nth-of-type(2)
   margin-left pxtorem(182px)
   font-size pxtorem(36px)
 .hide input:nth-of-type(1),.hide button 
    height pxtorem(93px)
    margin-left pxtorem(45px)
    border-radius pxtorem(20px) 
    font-size pxtorem(34px)
 .hide input:nth-of-type(1)
   width pxtorem(532px)
   margin-top pxtorem(70px)
   background yellow
   color black
   border pxtorem(5px) solid #292929
   padding-left  pxtorem(30px)   
 .hide button
    width pxtorem(562px)
    margin-top pxtorem(23px) 
    color white
    background #6ab439
    border pxtorem(1px) solid white
  .hide_big 
    position absolute
    top pxtorem(0px)
    left pxtorem(0px)
    height pxtorem(1350px)
    width pxtorem(750px)
    background #70706d
    opacity 0.6
    z-index 5

    
</style>
</style>



