<template>
  <div id="wrap">
    <div class='headPhoto'>
      <div class="inner">
        <span></span>
        <img src="../../assets/My/head.png" alt="">
        <img v-bind:src='mes[5]' alt="" class="man">
      </div>
      <div>{{mes[7]}}</div>
    </div>
    <div class="inner1">
      <ul class="follow">
        <li>关注<span>{{mes[0]}}</span></li>
        <li></li>
        <li>粉丝<span>{{mes[1]}}</span></li>
      </ul>
      <ul class="collection">
        <li>收藏<span>{{mes[2]}}</span></li>
        <li></li>
        <li>消息<span>{{mes[3]}}</span></li>
        <li></li>
        <li>可用积分<span>{{mes[4]}}</span></li>
      </ul>
    </div>
    <set-up class='setup'></set-up>
    <my-nav></my-nav>
  </div>
</template>

<script>
import SetUp from './SetUp.vue'
import Nav from '../public/Nav.vue'
import {mapGetters,mapActions} from "vuex"
export default {
  data () {
    return {

    }
  },
  //2.注册组件
  components:{
    "set-up":SetUp,
    "my-nav":Nav
  },

  created:function(){


  },
  //实时监听页面数据的变化
  computed:{
    mes:function(){
      console.log(this.$store.getters.getLoginMessage[5])
      return this.$store.getters.getLoginMessage;
    },
    bol:function(){
      return this.$store.getters.getLoginMessage[6];
    }
  },
  methods:{
    enterFn:function(){
      this.$router.push('/enter')
    }
  } 
}
</script>


<style lang='stylus' scoped>
  @import '../../assets/common/stylus/common.styl'
  #wrap
    width pxtorem(750px)
    height pxtorem(446px)
    border-bottom pxtorem(1px) solid #c5c4c4
    font-size pxtorem(26px)
    color #7e7e7e
    padding-top pxtorem(130px)
    .headPhoto
      text-align center
      .inner
        width pxtorem(170px)
        height pxtorem(170px)
        border pxtorem(8px) solid #c3ad88
        margin auto
        border-radius 50%
        position relative
        span
          display inline-block
          width pxtorem(150px)
          height pxtorem(150px)
          background-color #efeff4
          border-radius 50%
          position absolute
          top 0
          left 0
          right 0
          bottom 0
          margin auto
        img 
          width pxtorem(70px)
          height pxtorem(60px)
          position absolute
          top 0
          left 0
          right 0
          bottom 0
          margin auto
        .man
          width pxtorem(170px)
          height pxtorem(170px)
          border-radius 50%
      input
        width pxtorem(130px)
        height pxtorem(55px)
        border pxtorem(2px) solid #c3ad88  
        color #f1d47b
        background-color: #FFF
        border-radius pxtorem(10px)
        margin-top pxtorem(20px)
        margin-bottom pxtorem(20px)
      div
        min-height pxtorem(30px)
        margin-top pxtorem(35px)
        margin-bottom pxtorem(35px)
    .inner1
      width pxtorem(610px)
      margin auto
      text-align center
      .follow
        border-top pxtorem(1px) solid #c5c4c4
        li
          display inline-block
          width pxtorem(290px)
          height pxtorem(80px)
          line-height pxtorem(80px)
        li:nth-child(2)
          border pxtorem(1px) solid #c5c4c4
          width 0
          height pxtorem(60px)
          vertical-align middle
        li:nth-child(1)
          span
            margin-left pxtorem(20px)
            color #71f704
        li:nth-child(3)
          span
            color #ff4e00
            margin-left pxtorem(20px)
      .collection
        border-top pxtorem(1px) solid #c5c4c4 
        li
          display inline-block
          width pxtorem(190px)
          height pxtorem(80px)
          line-height pxtorem(80px)  
        li:nth-child(2n+2)
          border pxtorem(1px) solid #c5c4c4
          width 0
          height pxtorem(60px)
          vertical-align middle 
        li:nth-child(1)
          span
            margin-left pxtorem(20px)
            color #00deff
        li:nth-child(3)
          span
            color #c600ff
            margin-left pxtorem(20px)
        li:nth-child(5)
          span
            color #fc0320
            margin-left pxtorem(20px)
    .setup
      padding-top 0
</style>


