<template>
  <div class="wrap">
    <div class="currentLoca">
      <ul>
        <li>当前城市</li>
        <li>广州市</li>
      </ul>
    </div>
    <div class="provinces">
      <div>省市列表</div>
      <ul v-for='item in placeArr'>
        <li @click="CityFn">{{item}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
import {mapGetters,mapActions} from "vuex"
export default {
  data () {
    return {
      placeArr:["上海","上海郊区","江苏","浙江","安徽","北京","天津","河南","广东","广西","海南","青海","甘肃","西藏"]
    }
  },
  components:{

  },
  methods:{
    ...mapActions([
      "setCityName"]),
    CityFn:function(e){
      this.setCityName(e.target.firstChild.data);
      // this.$store.dispatch("setCityName",e.target.firstChild.data);
      this.$router.push('/')
    }
  }
}
</script>

<style lang='stylus' scoped>
  @import '../../assets/common/stylus/common.styl'
  .wrap
    width 100%
    font-size pxtorem(26px)
    text-align left
    background-color #f2f2f2
    .head2
      width 100%
      background-color #f29004
      color #fff
      font-size pxtorem(30px)
      font-weight bolder
      line-height pxtorem(85px)
      text-align center
      height pxtorem(85px)
      position fixed
      top pxtorem(40px)
      left 0
      z-index 10
      ul
        width pxtorem(710px)
        margin auto
        li:nth-child(1)
          float left
          img
            display inline-block
            width pxtorem(14px)
            margin-left pxtorem(10px)
        li:nth-child(2) 
          display inline-block
        li:nth-child(3)
          float right
          img
            display inline-block
            width pxtorem(40px)
            vertical-align middle
    .currentLoca
      width 100%
      margin auto
      ul
        width 100%
        li:nth-child(1)
          height pxtorem(80px)
          line-height pxtorem(80px)
          border-bottom pxtorem(1px) solid #e5e5e5
          padding-left pxtorem(20px)
        li:nth-child(2)
          height pxtorem(110px)
          line-height pxtorem(110px)
          border-bottom pxtorem(1px) solid #e5e5e5
          padding-left pxtorem(20px)
    .provinces
      width 100%
      margin auto
      div
        padding-left pxtorem(20px)
        height pxtorem(80px)
        line-height pxtorem(80px)
        border-bottom pxtorem(1px) solid #e5e5e5
        background-color #e6e6e6
      ul
        width 100%
        li
          height pxtorem(110px)
          line-height pxtorem(110px)
          border-bottom pxtorem(1px) solid #e5e5e5
          padding-left pxtorem(20px)
              
</style>



