<template>
  <div class="wrap">
    <div class="inner clear">
      <span>菜品精选</span>
    </div>
    <div class='innerBottom clear' v-for='item in choiceArr'>
      <div class="Left">
        <div class="text" @click='choiceFn'><p>{{item[0].title}}</p><p>￥{{item[0].originPrice}}</p></div>
        <img v-bind:src="item[0].src" alt="">
      </div>
      <div class="Right">
        <div  class="Rtop"><div class="text" @click='choiceFn1'><p>{{item[1].title}}</p><p>￥{{item[1].originPrice}}</p></div><img v-bind:src="item[1].src" alt=""></div>
        <div class="Rbottom">
          <div><div class="text" @click='choiceFn2'><p>{{item[2].title}}</p><p>￥{{item[2].originPrice}}</p></div><img v-bind:src="item[2].src" alt=""></div>
          <div><div class="text" @click='choiceFn3'><p>{{item[3].title}}</p><p>￥{{item[3].originPrice}}</p></div><img v-bind:src="item[3].src" alt=""></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import axios from 'axios';
import {mapGetters,mapActions} from "vuex";
export default {
  data () {
    return {
    }
  },
  //2.注册组件
  components:{

  },

  created:function(){
    
  },
  //实时监听页面数据的变化
  computed:mapGetters({
    choiceArr:'getChoice'
  }),
  methods:{
    ...mapActions([
    "getChoice","setShopCarGoods"]),
    choiceFn:function(){
      var arr=[];
      arr.push(this.choiceArr[0][0])
      this.setShopCarGoods(arr);
      this.$router.push("/goodsdetail")
    },
    choiceFn1:function(){
      var arr=[];
      arr.push(this.choiceArr[0][1])
      this.setShopCarGoods(arr);
      this.$router.push("/goodsdetail")
    },
    choiceFn2:function(){
      var arr=[];
      arr.push(this.choiceArr[0][2])
      this.setShopCarGoods(arr);
      this.$router.push("/goodsdetail")
    },
    choiceFn3:function(){
      var arr=[];
      arr.push(this.choiceArr[0][3])
      this.setShopCarGoods(arr);
      this.$router.push("/goodsdetail")
    }
  },
  mounted:function(){
 
  } 
  
}
</script>


<style lang='stylus' scoped>
  @import '../../assets/common/stylus/common.styl'
  .wrap
    width pxtorem(750px)
    margin auto
    margin-top pxtorem(20px)
    background-color #FFF
    padding-top pxtorem(30px)
    .inner
      width pxtorem(710px)
      margin auto
      font-size pxtorem(32px)
      font-weight bolder
      letter-spacing pxtorem(2px)
      color #92c808
      margin-bottom pxtorem(20px)
      span:nth-child(2)
        float right
        color #a0a0a0
    .innerBottom
      width pxtorem(750px)
      margin auto
      div
        float left
      .Left
        width pxtorem(290px)
        height pxtorem(300px)
        position relative
        .text
          position absolute
          top 0
          left 0
          color #fff
          background-color rgba(0, 0, 0,0.1)
          padding-top pxtorem(10px)
          padding-left pxtorem(15px)
          width 100%
          height 100%
        img
          width 100%
      .Right
        width pxtorem(460px)
        height pxtorem(300px)
        .Rtop
          width 100%
          height pxtorem(150px)
          position relative
          .text
            position absolute
            top 0
            left 0
            color #fff
            background-color rgba(0, 0, 0,0.1)
            padding-top pxtorem(10px)
            padding-left pxtorem(15px)
            width 100%
            height 100%
          img
            width 100%
        .Rbottom
          width 100%
          height pxtorem(150px)
          div
            display inline-block
            width pxtorem(230px)
            height 100%
            position relative
            .text
              position absolute
              top 0
              left 0
              color #fff
              background-color rgba(0, 0, 0,0.1)
              padding-top pxtorem(10px)
              padding-left pxtorem(15px)

            img
              width 100%
</style>


