<template>
  <div>
  <ul class="a" v-for='(item,index) in video' @mouseenter='showf =true' @mouseleave='showf = false' >
  <li>
  <video v-bind:src="item.url2"  controls
   @click="Vn(index)" v-show="!hide[index]"> 你的浏览器不支持video，请更新最新版本</video>
   </li>
  <li>
    <img v-bind:src="item.url1"  v-show="show[index]">
    <img v-bind:src="item.url3" @click="fn(index)" v-show="hide[index]">
    <img v-bind:src="item.url4" @click="fn(index)" v-show="!hide[index]">
    <p v-show='bol'></p>
  </li>
  <li>
   <p v-show="showhide[index]">{{item.title1}}</p> 
    <p v-show="showhide[index]"> {{item.title2}}</p>
 </li>

 <transition name='fade'>
            <p  v-show='!showf' class="allp">hello</p>
  </transition>
 </ul>
 
<h1>{{g}}</h1>
 </div>
</template>

<script>

import {mapGetters,mapActions} from "vuex"
export default {
   data(){
    return{
        g:"autoplay",
        show:[],
        hide:[],
        showhide:[],
        showf:true,
        bol:false 
    }
   },
   methods:{
        onMouseOver:function(){
           this.showp=!this.showp 
           console.log('asdsad')
        },
        fn:function(index){
           this.bol=!this.bol;
        // this.show[index]=!this.show[index]
        
              for(var i=0;i<$("video").length; i++){     
              if (index==i){    
           $("video").get(i).play();
           this.show[i]=!this.show[i]
           this.hide[i]=!this.hide[i]
           this.showhide[i]=!this.showhide[i]
            }
          else{
           $("video").get(i).pause();
           this.show[i]=true
           this.hide[i]=true
           this.showhide[i]=true
           }
           }
        },
        Vn:function(index){
            this.bol=!this.bol;
             this.show[index]=!this.show[index]
             this.hide[index]=!this.hide[index]
             this.showhide[i]=!this.showhide[i]
          if ($("video").get(index).paused){
           $("video").get(index).play();
         }
          else{
           $("video").get(index).pause();
           }
        },
         ...mapActions([
      "setVideo"]),
    },
   
   components:{ 
   },
   computed:mapGetters({
      video:'getVideo'
  }),
   mounted:function(){
        var show=[];
        var hide=[];
        var showhide=[];
        for(var i=0;i<this.video.length;i++){
                show.push(true)
                hide.push(true)
                showhide.push(true)
        }
        this.show=show,
        this.hide=hide,
        this.showhide=showhide;

   }
   
}
</script>

<style lang="stylus"  scoped>
@import '../../assets/common/stylus/common.styl'
div
   overflow hidden  
video 
   width pxtorem(750px)
   height pxtorem(421px)
   background-color #ebeae4 !important
.a li img:nth-of-type(1)
   position absolute
   top pxtorem(0px)
   left pxtorem(28px)
   width pxtorem(692px)
   height pxtorem(421px)
.a li img:nth-of-type(2),.a li img:nth-of-type(3) 
    width pxtorem(80px)
    height pxtorem(80px)
    position absolute
    z-index 1
    top pxtorem(170px)
    left 44%;
.a li img:nth-of-type(3)
    opacity 0.6

 .a li>p:nth-of-type(1)
     width pxtorem(630px)
     height pxtorem(50px)
     position absolute 
     top  pxtorem(70px) 
     left pxtorem(60px)
     text-align center
     font-size (18px)
     color white
     z-index 2
 .a li>p:nth-of-type(2)
     width pxtorem(630px)
     height pxtorem(50px)
     position absolute 
     top  pxtorem(120px) 
     left pxtorem(60px)
     text-align center
     font-size (20px)
     color white
     z-index 5

.allp
     height pxtorem(421px)
     width pxtorem(692px) 
     position absolute 
     top pxtorem(0px)
     left pxtorem(28px)
     background #403228
     opacity: 0.5
     z-index 1
  
 .fade-enter-active, .fade-leave-active {
            transition: opacity .5s
        }

        .fade-enter, .fade-leave-active {
            opacity: 0.3
        }
.a
   height pxtorem(471px)
   width pxtorem(750px) 
   position relative
.a:nth-of-type(1)
   margin-top pxtorem(150px)
 .a:last-of-type
   margin-bottom pxtorem(50px)  
div 
   background-color #ebeae4

</style>
</style>



