<template>
	<div class="coundown">
		<span>{{time.substring(0,2)}}</span>:
		<span>{{time.substring(2,4)}}</span>:	
		<span>{{time.substring(4,6)}}</span>	
	</div>
</template>
<script>
export default{
	data(){
		return {
			now: "",
		};
	},
	props: ['endtime'],
	methods:{
		dblFn:function(num,n){
			var len = num.toString().length;
			while (len<n) {
				num = "0" + num;
				len++;
			}
			return num;
		},
		formate(time){
			let hour = parseInt(time/1000/3600);
			let min = parseInt((time/1000 - hour * 3600)/60);
			let sec = parseInt(time/1000 - hour * 3600 - min * 60)
			var hour1=this.dblFn(hour,2);
			var minute1=this.dblFn(min,2);
			var second1=this.dblFn(sec,2);
			var str = String(hour1)+String(minute1)+String(second1);
			return str;

		},
		ready(){
		let self = this;
		setInterval(function(){
			self.now = new Date().getTime();
		  }, 1000);
		}
	},
	computed:{
		'time': function(){
			return this.formate(new Date(this.endtime).getTime() - this.now);
		}
	},
	mounted:function(){
		this.now=new Date().getTime();	
		this.ready();
	}
}
</script>
<style lang='stylus' scoped>
	@import '../../assets/common/stylus/common.styl'
		span
			display inline-block
			width pxtorem(43px)
			height pxtorem(43px)
			background-color: #292929
			color #FFF
			border-radius pxtorem(10px)
			vertical-align middle
			margin pxtorem(5px)
			line-height pxtorem(43px)
			font-size pxtorem(26px)
			text-align center
			margin-left pxtorem(-5px)
		span:nth-child(1)
			margin-left 0
</style>


