<template>
  <div class="wrap1">
    <my-head class='head'> 
      <strong slot='left'>
        <img src="../../assets/My/right.png" alt="" class="back" @click="backFn">
      </strong>
      <strong slot='center'>
        确认订单
      </strong>
    </my-head>
    <div class="content1">
      <ul>
        <li v-for='item in messageArr'>{{item}}</li>
      </ul>
      <div><img src="../../assets/My/left.png" alt=""></div>
      <img src="../../assets/My/tiaowen.png" alt="" class="tiaowen">
    </div>
    <div class="content2">
      <div class="content2_inner">
        <ul>
          <li v-for='item in shopArr'>
            <img v-bind:src="item.src" alt="">
            <p>{{item.goodsName}}</p>
            <p>￥{{item.goodsPrice}} x {{item.bum}}</p>
          </li>
        </ul>
      </div>
      <span>共{{count}}件</span>
      <img src="../../assets/My/left.png" alt="" class="left">
    </div>
    <div class="content3">
      <div class="content3_1">
        <span class="pay">支付配送</span>
        <div class="content3_1_inner">
          <p>支付方式</p>
          <p>快递方式</p>
          <div>
            <img src="../../assets/My/clock.png" alt="" class="clock">
            <span>工作日、双休日与假日均可送货</span>
          </div>
          
        </div>
        <img src="../../assets/My/left.png" alt="" class="payLeft">
      </div>
      <div class="content3_2">
        <span>优惠券</span><span>无可用</span>
        <span>积分兑换</span><span>无可用</span>
        <span>商品总金额</span><span>￥{{price}}</span>
        <span>运费</span><span>免邮</span>
      </div>
    </div>
    <div class="footer">
      <span>合计：￥{{price}}</span>
      <span @click='payMoneyFn'>立即支付</span>
    </div>
  </div>
</template>

<script>
import Head from '../Public/Header.vue'
// import axios from 'axios';
import {mapGetters,mapActions} from "vuex";
import { Toast } from 'mint-ui'
export default {
  data () {
    return {
      count:"",
      price:""
    }
  },
  //2.注册组件
  components:{
    "my-head":Head
  },

  created:function(){
    
  },
  //实时监听页面数据的变化
  computed:mapGetters({
    messageArr:'getMessageArr',
    shopArr:'getShopArr'
  }),
  methods:{
    ...mapActions([
    "getMessageArr",
    "getShopArr"
    ]),
    backFn:function(){
      this.$router.go(-1)
    },
    payMoneyFn:function(){
      var that = this;
      Toast({
        message: "支付成功",
        duration:1300
      });
      setTimeout(function(){
        that.$router.go(-1)
      }, 1500)
      
    }
  },
  mounted:function(){
    this.count=this.shopArr.length;
    var sum = 0;
    for(var i =0;i<this.shopArr.length;i++){
      sum += (this.shopArr[i].originPrice)*(this.shopArr[i].bum);
    }
    this.price=sum.toFixed(2);
  }
  
}
</script>


<style lang='stylus' scoped>
  @import '../../assets/common/stylus/common.styl'
  .wrap1
    width pxtorem(750px)
    font-size pxtorem(32px)
    .head
      strong
        font-size pxtorem(32px)
        .back
          width pxtorem(20px)
          vertical-align middle
    .content1
      background-color #ebeae4
      padding-top pxtorem(120px)
      height pxtorem(265px)
      ul
        display inline-block
        font-size pxtorem(32px)
        margin-top pxtorem(50px)
        margin-left pxtorem(20px)
        li:nth-child(1)
          display inline-block
          width pxtorem(250px)
          line-height pxtorem(100px)
        li:nth-child(2)
          display inline-block 
          
        li:nth-child(3)
          font-size pxtorem(26px)
          line-height pxtorem(50px) 
      .tiaowen
        margin-top pxtorem(40px)
        width pxtorem(750px)
      div
        float right
        margin-right pxtorem(20px)
        img
          margin-top pxtorem(120px)
          width pxtorem(25px)
    .content2
      width pxtorem(710px)
      padding pxtorem(10px) pxtorem(20px)
      border-bottom pxtorem(1px) solid #e5e5e5
      .content2_inner
        width pxtorem(585px)
        display inline-block
        overflow auto
        ul
          // width pxtorem(2000px)
          max-height pxtorem(300px)
          // overflow-x scroll
          li:nth-child(n+4)
            margin-top pxtorem(30px)
          li
            display inline-block
            font-size pxtorem(12px)
            width pxtorem(160px) 
            max-height pxtorem(300px)
            line-height pxtorem(30px)
            margin-right pxtorem(15px)
            img
              width pxtorem(160px)
              height pxtorem(180px)
            p:nth-child(3)
              color red
      span
        display inline-block
        vertical-align top
        margin-top pxtorem(110px)
        font-size pxtorem(26px)
      .left
        float right
        vertical-align top
        margin-top pxtorem(100px)
        width pxtorem(25px)
    .content3
      width pxtorem(730px)
      font-size pxtorem(26px)
      .pay
        display inline-block
        width pxtorem(120px)
        vertical-align top
        padding-left pxtorem(20px)
        padding-top pxtorem(20px)
      .content3_1_inner
        width pxtorem(530px)
        display inline-block
        text-align right
        line-height pxtorem(67px)
        .clock
          width pxtorem(30px)
          vertical-align middle
      .payLeft
        float right
        width pxtorem(25px)
        vertical-align top
        margin-top pxtorem(100px)
      .content3_2
        width pxtorem(750px)
        height pxtorem(78px)
        border-top  pxtorem(1px) solid #e5e5e5 
        line-height pxtorem(60px)
        span:nth-child(2n+1)
          display inline-block
          width pxtorem(170px) 
          border-bottom  pxtorem(1px) solid #e5e5e5
          text-indent pxtorem(20px)      
        span:nth-child(2n)
          display inline-block
          width pxtorem(557px) 
          text-align right
          color #a1a1a1
          font-size pxtorem(24px)
          letter-spacing pxtorem(3px) 
          border-bottom  pxtorem(1px) solid #e5e5e5 
          padding-right pxtorem(20px)
    .footer
      width pxtorem(750px)
      height pxtorem(120px)
      position fixed
      bottom 0
      left 0
      color #fff
      line-height pxtorem(120px)
      text-align center
      span:nth-child(1)
        float left
        height 100%
        width 65%
        background-color #65584d
      span:nth-child(2)
        float left
        height 100%
        width 35%
        background-color #ff5e39
        
      
    
          
        
</style>


