<template>
  <div class="wrap">
    <div class="logo"></div>
    <form action="">
      <input type="text" placeholder="用户名/邮箱/手机号" v-model='txt'>
      <input type="password" placeholder="密码" v-model='pwd'>
      <input type="button" value="登录" @click='LoginFn'>
    </form>
    <div class="order">
      <p>其他登录方式</p>
      <div v-for='item in orderEnterArr'><img v-bind:src="item" alt="" class="orderImg"></div>
    </div>
    <div class='forget'><span><img src="../../assets/My/mima.png" alt="">忘记密码</span><span><img src="../../assets/My/newUser.png" alt="">新用户注册</span></div>
  </div>
</template>

<script>
import {mapGetters,mapActions} from "vuex"
import { Toast } from 'mint-ui'
export default {
  data () {
    return {
      // num:{A:["36","17","5","39","15",require("../../assets/My/man1.png"),true,"宝宝别闹了"],B:["16","17","5","39","15",require("../../assets/My/man2.jpg"),true,"小机枪"],C:["56","17","5","39","15",require("../../assets/My/man3.jpg"),true,"小锋锋"],D:["46","17","5","39","15",require("../../assets/My/man4.jpg"),true,"小马哥"],E:["66","17","5","39","15",require("../../assets/My/man5.jpg"),true,"土豪强"]},
      num:["36","17","5","39","15",require("../../assets/My/man1.png"),true,""],
      loginBol:true,
      txt:"",
      pwd:""
    }
  },
  //2.注册组件
  components:{

  },

  created:function(){
    
  },
  //实时监听页面数据的变化
  computed:mapGetters({
    orderEnterArr:'getOrderEnterArr'
  }),
  methods:{
    ...mapActions([
      "setLoginMessage"
    ]),
    // randomFn:function(min,max){
    //   return Math.round(Math.random()*(max-min)+min)
    // },
    LoginFn:function(){
      // var random = this.randomFn(0,4);
      if (this.txt.length==0||this.pwd.length==0) {
        Toast({
          message: '信息未填写完整',
          duration:1300
        })
      }else {
        var arr=this.num;
        arr[7]=this.txt;
        this.setLoginMessage(arr);
        this.$router.push("/my");
      }
      
    }
  }
  
}
</script>


<style lang='stylus' scoped>
  @import '../../assets/common/stylus/common.styl'
  .wrap
    width pxtorem(750px)
    font-size pxtorem(32px)
    .logo
      width pxtorem(175px)
      height pxtorem(175px)
      background-color: #ebeae4
      border-radius pxtorem(45px)
      margin pxtorem(80px) auto pxtorem(50px)
    form
      input
        display block
        width pxtorem(710px)
        height pxtorem(62px)
        margin auto
        margin-top pxtorem(30px)
        border-bottom pxtorem(2px) solid #a1a1a1
        outline none
      input[type=button]
        height pxtorem(62px)
        margin-top pxtorem(80px)
        border none
        border-radius pxtorem(31px)
        background-color: #106602
        color #fff
    .order
      text-align center
      padding-top pxtorem(80px)
      p
        line-height pxtorem(80px)
        font-size pxtorem(26px)
      div
        display inline-block
        text-align center
        img
          display inline-block
          width pxtorem(60px)
          height pxtorem(60px)
          margin auto pxtorem(20px)
    .forget
      position fixed
      bottom 0
      left 0
      width pxtorem(710px)
      padding-left pxtorem(20px)
      line-height pxtorem(100px)
      font-size pxtorem(26px)
      span
        img
          display inline-block
          width pxtorem(30px)
          height pxtorem(30px)
          margin-right pxtorem(10px)
      span:nth-child(2)
        float right
           
      
</style>


