<template>
  <div class="vip">
    <my-head class='head'> 
      <strong slot='left'>
        <img src="../../assets/My/right.png" alt="" class="back" @click="backFn">
      </strong>
      <strong slot='center'>
        会员
      </strong>
      <strong slot='right' @click="rechargeFn">
        充值
      </strong>
    </my-head>
    <div class="cla" v-show='claBol'>
      <p class="txt">1.点击右上角可以进行充值</p>
      <p>2.会员等级及折扣:</p>
      <div class="tab clear" v-for='item in tabArr'>
        <span>{{item.vip}}</span>
        <span>{{item.zhe}}</span>
      </div>
    </div>
    <div class="cla" v-show='!claBol'>
      <p>1.会员充值说明：</p>
      <ul>
        <li v-for="(huiyuan,index) in huiyuanArr" @click='yuanFn(index)'>{{huiyuan}}</li>
      </ul>
    </div>
    <div class="rech" v-show='!claBol'>
      <p>2.充值方式：</p>
      <div class="recharge" v-for='(Img,index) in payArr'>
        <img :src="Img" alt="" @click='payFn(index)'>
      </div>
      <input type="button" value="确认支付" @click='btnFn'>
    </div>
    
  </div>
</template>

<script>
import Head from '../Public/Header.vue'
import { Toast } from 'mint-ui'
import {mapGetters,mapActions} from "vuex"
export default {
  data () {
    return {
      tabArr:[
        {vip:'会员等级',zhe:'折扣率'},
        {vip:'普通会员',zhe:'消费立享9折'},
        {vip:'高级会员',zhe:'消费立享8.8折'},
        {vip:'特高级会员',zhe:'消费立享8.5折'},
        {vip:'至尊会员',zhe:'消费立享8折'}
      ],
      huiyuanArr:["会员时间：1个月30元","会员时间：3个月80元","会员时间：6个月130元","会员时间：12个月200元"],
      claBol:true,
      payArr:[
        require("../../assets/My/pay1.png"),
        require("../../assets/My/pay2.png"),
        require("../../assets/My/pay3.png"),
        require("../../assets/My/pay4.png"),
        require("../../assets/My/pay5.png"),
        require("../../assets/My/pay6.png"),
        require("../../assets/My/pay7.png"),
        require("../../assets/My/pay8.png")
      ],
      AllBol:true,
      payTimeBol:false,
      payBol:false
    }
  },
  //2.注册组件
  components:{
    "my-head":Head
  },

  created:function(){
    
  },
  //实时监听页面数据的变化
  computed:{
    ...mapGetters({
      huiyuan:"getHuiYuan"
    })
  },
  methods:{
    ...mapActions([
      "setHuiYuan"
    ]),
     backFn:function(){
      this.$router.push("/my")
    },
    rechargeFn:function(){
      this.claBol=false;
    },
    tankuang:function(text){
      Toast({
        message: text,
        duration:1000
      })
    },
    yuanFn:function(index){
      switch (index) {
        case 0: this.tankuang("已选择1个月30元");break;
        case 1: this.tankuang("已选择3个月80元");break;
        case 2: this.tankuang("已选择6个月130元");break;
        case 3: this.tankuang("已选择12个月200元");break;
        default:break;
      }
      this.payTimeBol=true;
    },
    payFn:function(index){
      if (this.payTimeBol) {
        switch (index) {
          case 0: this.tankuang("已选用支付宝支付");break;
          case 1: this.tankuang("已选用中国银联支付");break;
          case 2: this.tankuang("已选用招商银行支付");break;
          case 3: this.tankuang("已选用中国银行支付");break;
          case 4: this.tankuang("已选用中国工商银行支付");break;
          case 5: this.tankuang("已选用中国建设银行支付");break;
          case 6: this.tankuang("已选用中国农业银行支付");break;
          case 7: this.tankuang("已选用广发银行支付");break;
          default:break;
        }
        this.setHuiYuan("您已经是会员了");
        this.payBol=true;
      }else {
        this.tankuang("未选择会员时间金额")
      }
      
    },
    btnFn:function(){
      if (this.payBol) {
        var that = this;
          this.tankuang("支付成功")
          var timer = setTimeout(function(){
            that.$router.push("/my");
          },1300)
      }else {
        this.tankuang("未选择支付方式")
      }
    }
      
  },
  mounted:function(){
    Toast({
      message: this.huiyuan,
      duration:1300
    })
  }
  
}
</script>


<style lang='stylus' scoped>
  @import '../../assets/common/stylus/common.styl'
  .vip
    width pxtorem(750px)
    .head
      strong
        font-size pxtorem(32px)
        .back
          width pxtorem(20px)
          vertical-align middle
    .cla:last-child
      padding-top pxtorem(30px)
      ul
        margin-left pxtorem(60px)
        font-size pxtorem(32px)
    .cla
      padding-top pxtorem(150px)
      font-size pxtorem(38px)
      line-height pxtorem(70px)
      padding-left pxtorem(20px)
      .tab:last-child
        border-bottom pxtorem(1px) solid #de6d48
      .tab
        border pxtorem(1px) solid #de6d48
        border-bottom none
        margin auto
        text-align center
        width pxtorem(604px)
        color red
        span:nth-child(2n+2)
          border-right none
        span
          float left
          width pxtorem(300px)
          height pxtorem(50px)
          border-right pxtorem(1px) solid #de6d48
          font-size pxtorem(32px)
          line-height pxtorem(50px)
      .txt
        margin-bottom pxtorem(30px)
    .rech
      width pxtorem(750px)
      text-align center
      padding-top pxtorem(30px)
      p
        text-align left
        margin-left pxtorem(20px)
        font-size pxtorem(38px)
      .recharge:nth-child(2n+1)
          margin-right pxtorem(20px)
      .recharge
        width pxtorem(320px)
        display inline-block
        img
          display inline-block
          width pxtorem(310px)
          height pxtorem(115px)
          margin-top pxtorem(20px)
      input[type=button]
        display inline-block
        width pxtorem(180px)
        height pxtorem(50px)
        margin-top pxtorem(50px)
        
</style>


