<template>
  <div class="">

     <img v-bind:src="url" @click="showToggle">
     <ul v-show="show">
        <li>{{data}}种可做菜品</li>
        <router-link :to="{path: './Menu'} "><button>查看</button></router-link>
     </ul>
  
  </div>
</template>

<script>

export default {
   data(){
    return{
    	  url:require("../../assets/Select/navv.png"),
        data:32,
        show:false
    }
   },
   methods:{
    showToggle:function(){
        this.show=!this.show
    }
   }
   
}
</script>

<style lang="stylus" scoped>
@import '../../assets/common/stylus/common.styl'
 ul

    width pxtorem(260px)
    height  pxtorem(260px)
    margin-top pxtorem(90px)
    background #e6e5dc
    overflow hidden
    margin-left pxtorem(-180px)
    
 li  
    width  pxtorem(220px)
    height pxtorem(30px)
    text-align center
    margin-left pxtorem(20px)
    margin-top pxtorem(50px)
 button
    height pxtorem(60px)
    width pxtorem(140px) 
    background  green
    color white
    border-radius pxtorem(20px)
    margin-top pxtorem(30px)
    margin-left pxtorem(60px)
    font-size pxtorem(30px)

 img
    width pxtorem(60px)
    height pxtorem(40px)
    margin-bottom pxtorem(100px)
    position fixed
    z-index 10
      
</style>



