<template>
	<div class="wrap">
		<div class='headPhoto'>
      <div class="inner">
        <span></span>
        <img src="../../assets/My/head.png" alt="" v-if='!bol'>
        <img v-bind:src='mes[5]' alt="" v-if='bol' class="man">
      </div>

      <input type="button" value="登录" @click='enterFn' v-if='!bol'>

      <div v-if='bol'>{{mes[7]}}</div>

	    <div class="inner1">
	      <ul class="follow">
	        <li>关注<span>{{mes[0]}}</span></li>
	        <li></li>
	        <li>粉丝<span>{{mes[1]}}</span></li>
	      </ul>
	      <ul class="collection">
	        <li>收藏<span>{{mes[2]}}</span></li>
	        <li></li>
	        <li>消息<span>{{mes[3]}}</span></li>
	        <li></li>
	        <li>可用积分<span>{{mes[4]}}</span></li>
	      </ul>
	    </div>
		</div>
		<div class="content clear" @click="myorderFn">
		    <span>全部订单</span>
		    <img src="../../assets/My/left.png" alt="">
		</div>
		<ul class="order">
		   <router-link v-for='(item,index) in orderArr' tag="li"  v-bind:to='item.src'>
		     	<img v-bind:src='item.url'>
		        {{item.title}}
		   </router-link>
		</ul>
		<ul class="set">
		  	<li v-for='(item,index) in setArr' tag="li"@click='detailPageFn(index)'>
		    	<img v-bind:src='item.url'>
		        {{item.title}}
		 	</li>
		</ul>
		<my-nav></my-nav> 
	</div>
</template>
<script>
import Nav from '../public/Nav.vue'
import {mapGetters,mapActions} from "vuex"
import { Toast } from 'mint-ui'
	export default{
		data (){
			return{

			}
		},
		components:{
			"my-nav":Nav
		},
		//实时监听页面数据的变化
	    computed:{
	    	mes:function(){
		       return this.$store.getters.getLoginMessage;
		    },
		    bol:function(){
		       return this.$store.getters.getLoginMessage[6];
		    },
		    ...mapGetters({
		    	orderArr:"getMyPage",
		    	setArr:"getSetArr"
		    })
	   },
	   methods:{
		  ...mapActions([
	      "getMyPage",
	      "getSetArr",
	      "setLoginMessage"
	      ]),
	   		enterFn:function(){
	   			this.loginBol=true;
		      this.$router.push('/enter')
		    }, 
		    logOffFn:function(){
		    	var se = confirm("要退出登录吗？");
		    	if (se==true) {
		    		this.setLoginMessage([""]);
		    	}else {
		    		return;
		    	}
		    },
		    detailPageFn:function(index){
		    	if (this.bol) {
		    		switch (index) {
		    			case 0:this.$router.push("/shopcar");break;
		    			case 1:this.$router.push("/myvip");break;
		    			case 2:this.$router.push("/service");break;
		    			case 3:this.$router.push("/set");break;
		    			case 4:this.logOffFn();break
		    			default: ;break;
		    		}
		    	}else {
		    		Toast({
			          message: "用户未登录",
			          duration:1300
			        })
		    	}
		    },
		    myorderFn:function(){
		    	if (this.bol) {
		    		console.log('123')
		    		this.$router.push('/myorder')
		    	}else {
		    		Toast({
			          message: "用户未登录",
			          duration:1300
			        })
		    	} 
		    }
	   },
	   mounted:function(){

	  }
	}
</script>
<style lang='stylus' scoped>
	@import '../../assets/common/stylus/common.styl'
	  .wrap
	    width pxtorem(750px)
	    min-height pxtorem(1234px)
	    font-size pxtorem(26px)
	    color #7e7e7e
	    text-align center
	    padding-top pxtorem(100px)
	    .headPhoto
	      .inner
	        width pxtorem(170px)
	        height pxtorem(170px)
	        border pxtorem(8px) solid #c3ad88
	        margin auto
	        border-radius 50%
	        position relative
	        text-align center
	        span
	          display inline-block
	          width pxtorem(150px)
	          height pxtorem(150px)
	          background-color #efeff4
	          border-radius 50%
	          position absolute
	          top 0
	          left 0
	          right 0
	          bottom 0
	          margin auto
	        img 
	          width pxtorem(70px)
	          height pxtorem(60px)
	          position absolute
	          top 0
	          left 0
	          right 0
	          bottom 0
	          margin auto
	        .man
	          width pxtorem(170px)
	          height pxtorem(170px)
	          border-radius 50%
	      input
	        width pxtorem(130px)
	        height pxtorem(55px)
	        border pxtorem(2px) solid #c3ad88  
	        color #f1d47b
	        background-color: #FFF
	        border-radius pxtorem(10px)
	        margin-top pxtorem(10px)
	      div
	        margin-top pxtorem(35px)
	        margin-bottom pxtorem(35px)
	    .inner1
	      width pxtorem(610px)
	      margin auto
	      .follow
	        border-top pxtorem(1px) solid #c5c4c4
	        li
	          display inline-block
	          width pxtorem(290px)
	          height pxtorem(80px)
	          line-height pxtorem(80px)
	        li:nth-child(2)
	          border pxtorem(1px) solid #c5c4c4
	          width 0
	          height pxtorem(60px)
	          vertical-align middle
	        li:nth-child(1)
	          span
	            margin-left pxtorem(20px)
	            color #71f704
	        li:nth-child(3)
	          span
	            color #ff4e00
	            margin-left pxtorem(20px)
	      .collection
	        border-top pxtorem(1px) solid #c5c4c4 
	        border-bottom pxtorem(1px) solid #c5c4c4 
	        li
	          display inline-block
	          width pxtorem(190px)
	          height pxtorem(80px)
	          line-height pxtorem(80px)  
	        li:nth-child(2n+2)
	          border pxtorem(1px) solid #c5c4c4
	          width 0
	          height pxtorem(60px)
	          vertical-align middle 
	        li:nth-child(1)
	          span
	            margin-left pxtorem(20px)
	            color #00deff
	        li:nth-child(3)
	          span
	            color #c600ff
	            margin-left pxtorem(20px)
	        li:nth-child(5)
	          span
	            color #fc0320
	            margin-left pxtorem(20px)
	.content
	  border-bottom pxtorem(1px) solid #ebeae4
	  width pxtorem(710px)
	  margin auto
	  height pxtorem(70px)
	  line-height pxtorem(70px)
	  text-align left
	  img
	  	width pxtorem(15px)
	  	float right
	  	margin-top pxtorem(20px)
	.order
		text-align center
		margin pxtorem(20px) 0
		color #7e7e7e
		li
			display inline-block
			width pxtorem(110px)
			margin 0 pxtorem(30px)
			img
				width pxtorem(90px)
	.set
		width pxtorem(750px)
		text-align left
		li:last-child
			border-bottom pxtorem(1px) solid #ebeae4
		li
			border-top pxtorem(1px) solid #ebeae4
			line-height pxtorem(70px)
			padding 0 pxtorem(20px)
			img
				display inline-block
				width pxtorem(40px)
				vertical-align middle
				margin-right pxtorem(10px)
</style>



