<template>
  <div class="circle">
  <ul v-for="(item,index) in CicleArr" class="all">
    <li class="Head_img"><img v-bind:src="item.url1"></li>
     <li class="Head_content">
        <img v-bind:src="item.url2" >
       
        <span>{{item.title}}</span>
        <span>2016.09.23 21:26 </span>
        <span @click="fn(index)" class="colle" >收藏</span>
        
    </li>
    <li class="Head_btm">
      <span>{{item.head_title1}}</span>
      <span>{{item.head_title2}}</span> 
    </li> 
   <li class="last_img">
      <img v-bind:src="item.url3" v-show="show[index]" @click=heart(index)>
      <img v-bind:src="item.url3_3" class="Head_contentsmall" v-show="!show[index]" @click=heart(index)>
  <router-link :to="{path: './evaluate'} " class="second_routImge"><img v-bind:src="item.url4" ></router-link>
  <router-link :to="{path: './Circlecomment'} " class="last_routImge"><img v-bind:src="item.url5" ></router-link>
  <p v-show="hide"></p>
    </li>
  </ul>
    
  </div>
</template>

<script>
import Nav from '../Public/Nav.vue';
import { Toast } from 'mint-ui'
import {mapGetters,mapActions} from "vuex"
export default {
   data(){
    return{
       show:[],
       collect:[],
       hide:true        
    }
   },
   methods:{
     heart:function(index){
      this.show[index]=!this.show[index]
      this.hide=!this.hide
      console.log('aa')
      if (this.show[index]==false) {
        Toast({
          message: '点赞成功',
          iconClass: 'icon icon-success',
          duration:1300
        });
        return;
      }else {
           Toast({
          message: '取消点赞',
          iconClass: 'icon icon-success',
          duration:1300
            });  
        }
     },
     fn:function(index){
       this.collect[index]=!this.collect[index];
     var colle=document.querySelectorAll(".colle");
       if(this.collect[index]==true){
         colle[index].style.background = 'orange'

     Toast({
          message: '收藏成功',
          iconClass: 'icon icon-success',
          duration:1300
        });
        return;
       }else{
          colle[index].style.background = ''
        Toast({
          message: '取消收藏',
          iconClass: 'icon icon-success',
          duration:1300
        });
      }   
     },
     ...mapActions([
      "setCicleArr"])

   },
   // 注册组件
   components:{
     
   },
    computed:mapGetters({
      CicleArr:'getCicleArr'
  }),
    mounted:function(){
      var show=[];
      var collect=[];
      for(var i=0;i<this.CicleArr.length; i++)
      {
         show.push(true)
         collect.push(false)
      }
      this.show=show,
      this.collect=collect

    }
}
</script>

<style lang="stylus"  scoped>
@import '../../assets/common/stylus/common.styl'
.circle
     width pxtorem(750px)
     background #ebeae4
.all
     width pxtorem(750px)
     overflow hidden
     margin 0
     padding 0
     position relative
.Head_img,.Head_img img
     width 100%
     height pxtorem(359px)
.Head_img
    margin-top pxtorem(15px)
.Head_content
    position absolute
    top  pxtorem(29px)
    left pxtorem(19px)
    color white     
.Head_content span:nth-of-type(1)
    position absolute
    top pxtorem(10px) 
.Head_content span:nth-of-type(3)
    position absolute
    text-align center
    top pxtorem(10px)
    left pxtorem(650px)
    width pxtorem(70px)
    height pxtorem(45px)
    border pxtorem(1px) solid white
    border-radius  pxtorem(11px) 
    line-height pxtorem(45px)
    font-size pxtorem(20px)
 .Head_content img 
    width   pxtorem(62px)
    height pxtorem(62px)
 
.Head_btm
    width 100%
    height pxtorem(97px)
    overflow hidden
    border-bottom pxtorem(2px) solid #dadada
    text-align left
    background white
.Head_btm span:nth-of-type(1)
    margin-top pxtorem(19px)
    margin-bottom  pxtorem(5px)
    font-size pxtorem(28px)
    display block
    margin-left pxtorem(20px)
    font-weight bold 
.Head_btm span:nth-of-type(2)      
     font-size pxtorem(21px)
     margin-left pxtorem(20px)
     color #a9a9a9
.last_img
     width 100%
     height pxtorem(67px)
     text-align left
     background white
     position relative
 .last_img>.Head_contentsmall
     left pxtorem(28px)
     top pxtorem(17px)     
.last_img img:nth-of-type(1),.last_img>.Head_contentsmall,.second_routImge
.last_routImge
      width pxtorem(40px)
      height pxtorem(40px) 
      position absolute 
.last_img img:nth-of-type(1)
      left pxtorem(28px)
      top pxtorem(18px)
.second_routImge
      left pxtorem(110px)
      top pxtorem(0px)
.last_routImge 
      left pxtorem(580px)
      top pxtorem(-2px)
    
.circle
      margin-bottom  pxtorem(110px)

</style>
</style>



