<template>
  <div class="wrap1">
    <my-head class='head'> 
      <strong slot='left'>
        <img src="../../assets/My/right.png" alt="" class="back" @click="backFn">
      </strong>
      <strong slot='center'>
        设置
      </strong>
    </my-head>
    <ul>
      <li v-for='(item,index) in setArr' @click='SetDetailFn(index)'>
        <img v-bind:src='item.url'>
        {{item.title}}
        <img v-bind:src='item.url1' alt="">
      </li>
    </ul>
  </div>
</template>

<script>
import Head from '../Public/Header.vue'
import { MessageBox ,Indicator} from 'mint-ui';
// import axios from 'axios';
import {mapGetters,mapActions} from "vuex";
export default {
  data () {
    return {
    }
  },
  //2.注册组件
  components:{
    "my-head":Head
  },
  created:function(){
    
  },
  //实时监听页面数据的变化
  computed:mapGetters({
    setArr:'getSetArrPage'
  }),
  methods:{
    ...mapActions([
    "getSetArrPage"]),
    backFn:function(){
      this.$router.push("/my")
    },
    feedbackFn:function(){
      MessageBox.prompt('提示', '请输入您的宝贵意见');
    },
    updateFn:function(){
      MessageBox.alert('您的版本已经是最新了,无需更新');
    },
    clearFn:function(){
      Indicator.open('缓存清理中...');
      var timer = setTimeout(function(){
        MessageBox.alert("提示","缓存清理完成")
        Indicator.close();
      },5000)
    },
    SetDetailFn:function(index){
      switch (index) {
        case 0:this.$router.push("/help");break;
        case 1:this.feedbackFn();break;
        case 2:this.clearFn();break;
        case 3:this.updateFn();break;
        default:break;
      }
    }
  } 
}
</script>
<style lang='stylus' scoped>
  @import '../../assets/common/stylus/common.styl'
  .wrap1
    width pxtorem(750px)
    .head
      strong
        font-size pxtorem(32px)
        .back
          width pxtorem(20px)
          vertical-align middle
    ul
      width pxtorem(750px)
      background-color #ebeae4
      padding-top pxtorem(140px)
      li:nth-child(4)
        margin-top pxtorem(20px)
      li
        width pxtorem(750px)
        height pxtorem(79px)
        border-bottom pxtorem(1px) solid #c5c4c4
        background-color: #FFF
        line-height pxtorem(80px)
        font-size pxtorem(26px)
        img:nth-child(1)
          width pxtorem(40px)
          vertical-align middle
          margin-left pxtorem(20px)
          margin-right pxtorem(20px)
        img:nth-child(2)
          width pxtorem(18px) 
          float right
          vertical-align middle
          margin-right pxtorem(20px)
          margin-top pxtorem(28px)
</style>


