<template>
  <div class="">
    <my-head class='head'> 
      <strong slot='center'>
        <a href="###" @click="fn_one">圈子</a>
        <a href="###" @click="fn_two">视频</a>
      </strong>
    </my-head>

    <my-circle class="a" v-show="show"></my-circle>
    <my-video  v-show="!show"></my-video>
    <my-nav class="footer"></my-nav>
    <!-- <my-comment ></my-comment> -->
  </div>
</template>

<script>
import Head from '../Public/Header.vue'
import Nav from '../Public/Nav.vue';
import Circle from './Circle.vue';
import Video from './Video.vue';

// import comment from './Circlecomment.vue' 
export default {
   data(){
    return{
      show:true
    }
   },
   methods:{
     fn_one:function(){
         this.show=true
    },
    fn_two:function(){
         this.show=false
    }
   },
   components:{
    "my-head":Head,
    'my-nav':Nav,
    'my-circle':Circle,
    "my-video":Video
    // 'my-comment':comment
   }
}
</script>

<style lang="stylus"  scoped>
@import '../../assets/common/stylus/common.styl'
  .head
    strong
      text-align center 
      a
        display inline-block
        width pxtorem(120px)
        line-height pxtorem(60px)
        color #fff
        background-color: orange
      a:nth-child(2)
        margin-left pxtorem(-8px) 
        border-radius 0 pxtorem(20px) pxtorem(20px) 0
      a:nth-child(1)
        border-radius pxtorem(20px) 0 0 pxtorem(20px)
 .a
   margin-top pxtorem(120px)
.footer 
     z-index 10
</style>
</style>



