<template>
  <div class="myOrder">
    <my-head class='head'> 
      <strong slot='left'>
        <img src="../../assets/My/right.png" alt="" class="back" @click="backFn">
      </strong>
      <strong slot='center'>
        我的订单
      </strong>
    </my-head>
    <div class="cont1">
      <a href="###" v-for='(item,index) in contArr' @click='payFn(index)'>{{item}}</a>
    </div>
  </div>
</template>

<script>
import Head from '../Public/Header.vue'
export default {
  data () {
    return {
      contArr:["待付款","待发货","待收货","已完成","退货/售后"]
    }
  },
  //2.注册组件
  components:{
    "my-head":Head
  },

  created:function(){
    
  },
  //实时监听页面数据的变化
  computed:{
    
  },
  methods:{
    backFn:function(){
      this.$router.push("/my")
    },
    payFn:function(index){
      $(".cont1>a").css({"borderBottomColor":"transparent"})
      $('.cont1>a').eq(index).css({"borderBottomColor":"#0ac42d"})
    }
  }
  
}
</script>


<style lang='stylus' scoped>
  @import '../../assets/common/stylus/common.styl'
  .myOrder
    width pxtorem(750px)
    font-size pxtorem(32px)
    .head
      strong
        font-size pxtorem(32px)
        .back
          width pxtorem(20px)
          vertical-align middle
    .cont1
      border-bottom pxtorem(1px) solid #ccc
      height pxtorem(60px)
      padding pxtorem(120) pxtorem(20px) 0
      a:last-child
        margin-right 0
      a:first-child
        border-bottom pxtorem(5px) solid #0ac42d
      a
        display inline-block
        height pxtorem(45px)
        line-height pxtorem(45px)
        border-bottom pxtorem(5px) solid transparent
        margin-right pxtorem(45px)
        vertical-align middle
</style>


