<template>
  <div class="wrap">
    <div class="inner clearfix">
      <span>
        <slot name='left'></slot>
      </span>
      <span>
        <slot name='center'></slot>
      </span>
      <span>
        <slot name='right'></slot>
      </span>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {

    }
  }
}
</script>


<style lang='stylus' scoped>
  @import '../../assets/common/stylus/common.styl'
  .wrap
    width 100%
    height pxtorem(120px)
    background-color #106602
    color #fff
    line-height pxtorem(120px)
    position fixed
    top 0
    left 0
    z-index 10
    // text-align center
    .inner
      width pxtorem(710px)
      line-height pxtorem(120px)
      margin auto
      text-align center
      position relative
      span:nth-child(1)
        float left
      span:nth-child(3)
        float right
</style>


